﻿import { Api } from "lib/api";
import { rset } from "lib/utils";

export class DB
{
   static async query(sql: string)
   {  
      return await Api.call<any[]>("DB.Query", sql);
   }

   static async queryColumn(sql: string)
   {  
      const risp = await Api.call<any[]>("DB.Query", sql);

      let arr: any[] = [];

      if(risp.length > 0) {
         let firstRow = risp[0];
         let firstField = Object.keys(firstRow)[0];
         arr = risp.map((row: any) => row[firstField]);
      }

      return arr;
   }
}

export class SQL
{
   static get mindate()
   {
      return new Date(1899,11,30,0,0,0);
   }

   static num(value: any) {
      let n = Number(value);
      if(isNaN(n)) n = 0;
      return String(n);
   }

   static string(value: string|null|undefined) {
      let s = value ? value : '';
      return "'" + s.split("'").join("''") + "'";
   }

   static date(value: Date|null|undefined) {
      let v = value ? value : SQL.mindate;  
      const year  = rset(v.getFullYear(),4,"0");
      const month = rset(v.getMonth()+1,2,"0");
      const day   = rset(v.getDate(),2,"0");
      return `CONVERT(DATETIME,'${year}-${month}-${day} 00:00:00',102)`;                    
   }

   static datetime(value: Date|null|undefined) {
      let v = value ? value : SQL.mindate;  
      const year  = rset(v.getFullYear(),4,"0");
      const month = rset(v.getMonth()+1,2,"0");
      const day   = rset(v.getDate(),2,"0");
      const hour  = rset(v.getHours(),2,"0");
      const min   = rset(v.getMinutes(),2,"0");
      const sec   = rset(v.getSeconds(),2,"0");
      return `CONVERT(DATETIME,'${year}-${month}-${day} ${hour}:${min}:${sec}',102)`;                    
   }

}





