﻿let counter = 0;

export class Observable
{
   listeners: Array<{event: string, tocall: ()=>void, id: number}> = [];

   on(event: string, tocall: ()=>void): number
   {
      counter++;
      this.listeners.push({event, tocall, id: counter});
      return counter;
   }

   off(id: number)
   {
      this.listeners = this.listeners.filter(e=>e.id !== id);
   }

   fire(event: string)
   {
      this.listeners.forEach(e => {
         if(e.event === event) e.tocall();
      });
   }
}




