﻿import React = require("react");

interface IProps
{
   icon: string;
   iconsize: string;

   className?: any;
}

export const Icon = (props: IProps) => 
{
   let sizeClass = props.iconsize ? "fa-"+props.iconsize : "";

   // takes class specified in html
   let oldClass = props.className ? props.className : "";  

   // remove "className" from remaining props
   let otherProps = { ...props } as any;
   delete otherProps.className;

   return <i className={`fa fa-${props.icon} ${sizeClass} ${oldClass}`} aria-hidden="true" {...otherProps}></i>; 
} 



