﻿import Rioct = require("rioct");

import { MainTag } from "tags/main-tag/main-tag";

// #if DEBUG
import { runJasmineTests } from "./specRunner";
// #endif

function startApp()
{
   // jasmine boot.js links to window.onload 
   var oldWindowOnLoad = window.onload;

   window.onload = () => 
   { 
      if(document.getElementById("put_here")) 
      {
         if(DEBUG)
         {
            runJasmineTests();
         }
      }     
      else 
      {
         startNormal();
      }
      if(oldWindowOnLoad) oldWindowOnLoad.call(window, null);      
   }
}

function startNormal()
{
	try
	{ 
      Rioct.setStyleParser((css: string) => {
         const styleVariables = {                        
            footerBgColor: "#345679"
         };

         if(DEBUG)
         {
            styleVariables.footerBgColor = "#ee3311";         
         }

         // replace variable with value
         Object.keys(styleVariables).forEach(key => css = css.replaceAll(`var(--${key})`, styleVariables[key]));

         return css;
      });  
      Rioct.updateStyles();             
		Rioct.mount("main-tag", MainTag);      			
   }
	catch(ex)
	{        
	   console.log("rioct initialization error");
      console.log(ex);	
	} 
}

export { startApp };



