﻿import { Store } from "lib/store";
import Rioct = require("rioct");

import { Api, ServerError, isFatalError } from "lib/api";
import { AppRoutes }from "tags/main-tag/appRoutes";


/**
 * Reminder veloce: 
 * - userInfo: utente che si è realmente collegato
 * - superUserInfo: utente del quale si impersonifica l'identita
 * - UI.isFaking è true se l'utente UI sta impersonificando SUI
 */

export interface IUserInfo
{
   Matricola:     number;
   Username:      string;
   PIN:           string;
   Cognome:       string;
   Nome:          string;
   Nominativo:    string;
   Ufficio:       string;
   Posizione:     number;
   Livello1:      number;
   Settore:       string;
   isFaking:      bool; 
   Token:         string;
   SessionId:     string;
}

class UserInfo implements IUserInfo
{
   Matricola     = 0;
   Username      = "";
   PIN           = "";
   Cognome       = "";
   Nome          = "";
   Nominativo    = "";
   Ufficio       = "";
   Posizione     = 0;
   Livello1      = 0;
   Settore       = "";
   isFaking      = false; 
   Token         = "";
   SessionId     = "";

   get isLogged() 
   { 
      return this.Matricola !== 0; 
   }

   hasToken(token: TokenPermessi)
   {            
      return this.Matricola !== 0 ? this.Token.contains(token) : false;
   }

   /*
   hasTokenInPosizione(token: TokenPermessi)
   {            
      return this.Matricola !== 0 ? this.isGoodPosition(token, this.Posizione) : false;
   }

	isGoodPosition(token: string, pos: number): boolean {
      const R = this.makeRanges(token);         
		for(const range of R) {
         if(pos>=range.start && pos<=range.end) return true;
      }
      return false;
	}
   */

   hasTokenInSettore(token: TokenPermessi) {
      const settore = this.Posizione - (this.Posizione % 100);
      const rs = { start: settore, end: settore + 99};

      const R = this.makeRanges(token);         
		for(const range of R) {
         if(this.intersect(rs,range)) return true;
      }
      return false;
   }

   private intersect(r1: Range, r2: Range): boolean
   {
      if(r1.end < r2.start) return false;
      if(r1.start > r2.end) return false;
      return true;
   }

	private getTokenDefs(token: string): string {
		const tokens = this.Token.split(' ');
		for(const tok of tokens) {
			if(tok.contains(token)) {
            const x = tok.indexOf('(');
            const y = tok.indexOf(')');
            if(x==-1 || y==-1) throw new Error("token `${tok}` without braces");
				return tok.substr(x+1,y-x-1);
			}
		}
		return "";
	}     
  
   private makeRanges(token: string): Range[] {
      const Ranges: Range[] = [];

      const defs = this.getTokenDefs(token);
      if(defs=="") return Ranges;

		for(const range of defs.split(',')) {
         let start, end;
			if(range.indexOf("-")!==-1) {
				const s = range.split('-');
				start = Number(s[0]);
				end   = Number(s[1]);				   
			}
			else {
				start = Number(range);
            end = start;
			}
         Ranges.push({start, end});         
      }
      return Ranges;
   }
}

export interface UserResult
{
   user: UserInfo;
   superUser: UserInfo;
}

class UserManager extends Store implements UserResult
{
   user = new UserInfo();
   superUser = new UserInfo();

   data = {
      user: this.user,
      superUser: this.superUser
   };

   constructor()
   {
      super("user-manager");
      this.update();
   }

   loadFromResult(result: UserResult)
   {
      this.loadFromServer(this.user, result.user);
      this.loadFromServer(this.superUser, result.superUser);
      this.update();
   }

   loadFromServer(dest: IUserInfo, ui: IUserInfo)
   {
      dest.Matricola     = ui.Matricola      ;
      dest.Username      = ui.Username       ;
      dest.PIN           = ui.PIN            ;
      dest.Cognome       = ui.Cognome        ;
      dest.Nome          = ui.Nome           ;
      dest.Nominativo    = ui.Nominativo     ;
      dest.Ufficio       = ui.Ufficio        ;
      dest.Posizione     = ui.Posizione      ;
      dest.Livello1      = ui.Livello1       ;
      dest.Settore       = ui.Settore        ;
      dest.isFaking      = ui.isFaking       ;
      dest.Token         = ui.Token          ;
      dest.SessionId     = ui.SessionId      ;

      // writes cookie
      if(dest.SessionId)
      {
         document.cookie = `sessionid=${dest.SessionId};expires=${new Date(2099,1,1).toUTCString()}`;
      }
   }

   async SU_posizione(Posizione: number)
   {
      const risp = await Api.call<UserResult>("ChangeSettore", Posizione);
      this.loadFromResult(risp);
   }

   async SU_matricola(matricola: number)
   {
      const risp = await Api.call<UserResult>("SU_Matricola", matricola);
      this.loadFromResult(risp);
   }

   async DropIdentity()
   {
      const risp = await Api.call<UserResult>("drop-identity");
      this.loadFromResult(risp);
   }

   async bodySnatch(matricola: number) {
      if(matricola != this.user.Matricola) {
         try
         {
            await userManager.SU_matricola(matricola);
         }
         catch(err)
         {
            if(err instanceof ServerError) {
               if(!isFatalError(err)) {
                  AppRoutes.forbidden();
                  return;
               }
            }
         }
      }
   }
}

const userManager = new UserManager();

function hasToken(token: TokenPermessi): boolean
{
   return userManager.user.Token.contains(token);           
}

type TokenPermessi = "cmal" | "presenze" | "admin_users" | "responsabile" | "segreteria" | "dirigente" | "risorseumane" | "fase1" | "debug";

export { userManager, hasToken, TokenPermessi };

interface Range
{
   start: number;
   end: number;
}




