      "0074" : [ `L'agente del provvedimento amministrativo:`, `Può essere un'autorità amministrativa, un privato esercente una pubblica funzione, ovvero un privato obbligato a svolgere un procedimento di evidenza pubblica.`, `Può essere solo un'autorità amministrativa ovvero un privato esercente una pubblica funzione.`, `Può essere solo un'autorità amministrativa ovvero un privato obbligato a svolgere un procedimento di evidenza pubblica.` ] , 
