      "0473" : [ `Abilitazioni, licenze, omologazioni, nulla-osta, sono figure affini all'autorizzazione. Le abilitazioni:`, `Sono provvedimenti di tipo permissivo nei quali viene in rilievo l'esercizio di discrezionalità tecnica.`, `Sono provvedimenti che, intervenendo necessariamente in un momento successivo a quello in cui è adottato l'atto, funzionano come condizione di efficacia sullo stesso.`, `Sono atti con cui la p.a. dichiara di non avere nulla in contrario in ordine all'adozione di un provvedimento da parte di un'altra autorità.` ] , 
