      "0477" : [ `Abilitazioni, licenze, omologazioni, nulla-osta, sono figure affini all'autorizzazione. I nulla-osta:`, `Sono atti con cui la p.a. dichiara di non avere nulla in contrario in ordine all'adozione di un provvedimento da parte di un'altra autorità.`, `Sono provvedimenti mediante i quali i privati sono abilitati ad esercitare l'attività in deroga al divieto legale oppure sono esonerati dal compimento di un'attività doverosa.`, `Sono rilasciate dall'autorità a suggello della conformità delle cose alle norme nazionali e internazionali a tutela della sicurezza o dell'uniformità dei prodotti.` ] , 
