      "0486" : [ `Mediante quale dei seguenti provvedimenti la P.A. attribuisce al destinatario il diritto-dovere di svolgere in via esclusiva un'attività o di godere di un determinato bene riservati originariamente alla P.A.?`, `Concessione.`, `Autorizzazione.`, `Appalto.` ] , 
      "0487" : [ `La concessione:`, `È un provvedimento amministrativo che attribuisce al destinatario, ampliandone così la sfera giuridica,  il diritto-dovere di svolgere in via esclusiva un'attività o di godere di un determinato bene riservati originariamente alla P.A.`, `Costituisce un atto di consenso all'esercizio di un'attività o di un potere di cui il soggetto pubblico o privato richiedente è titolare in virtù di una posizione giuridica soggettiva riconosciutagli dall'ordinamento.`, `È un atto di controllo preventivo di merito sull'atto o sulla attività compiuta.` ] , 
      "0488" : [ `Le concessioni traslative:`, `Trasferiscono al concessionario un diritto appartenente all'amministrazione.`, `Attribuiscono un diritto acquistabile da parte di soggetti anche privati dotati dei requisiti richiesti dalla legge esclusivamente attraverso l'atto amministrativo.`, `Si sostanziano nell'iscrizione agli albi professionali.` ] , 
