      "0491" : [ `Con riferimento all'efficacia delle concessioni, quando attribuiscono un diritto acquistabile da parte di soggetti anche privati dotati dei requisiti richiesti dalla legge esclusivamente attraverso l'atto amministrativo sono dette:`, `Costitutive.`, `Traslative.`, `Modali.` ] , 
