      "0500" : [ `"Indicare quale affermazione sulle “concessioni” è corretta."`, `La concessione ha natura costitutiva, in quanto attribuisce al privato un diritto di cui prima non era titolare.`, `La concessione si limita a rimuovere un ostacolo all'esercizio di un diritto potenziale.`, `Nella concessione la P.A. mantiene solo un potere di vigilanza sullo svolgimento dell'attività a seguito del quale può adottare solo provvedimenti sanzionatori.` ] , 
      "0501" : [ `"Indicare quale affermazione sulle “concessioni” è corretta."`, `Nella concessione vi è un potere di ingerenza della P.A. nello svolgimento del servizio che si esprime nel potere di direzione, sostituzione e revoca.`, `La concessione si limita a rimuovere un ostacolo all'esercizio di un diritto potenziale.`, `Nella concessione la P.A. mantiene solo un potere di vigilanza sullo svolgimento dell'attività a seguito del quale può adottare solo provvedimenti sanzionatori.` ] , 
      "0502" : [ `"Indicare quale affermazione sulle “concessioni” è corretta."`, `"Nel rapporto concessorio l'amministrazione attribuisce al terzo il potere di svolgere una determinata attività in via esclusiva, assegnandogli una posizione di monopolio odoligopolio."`, `La concessione si limita a rimuovere un ostacolo all'esercizio di un diritto potenziale.`, `Nella concessione la P.A. mantiene solo un potere di vigilanza sullo svolgimento dell'attività a seguito della quale può adottare solo provvedimenti sanzionatori.` ] , 
      "0503" : [ `Indicare quale affermazione afferente le distinzioni che intercorrono tra autorizzazione e concessione e corretta.`, `A differenza dell'autorizzazione, che si limita a rimuovere un ostacolo all'esercizio di un diritto potenziale, la concessione ha natura costitutiva, in quanto attribuisce al privato un diritto di cui prima non era titolare.`, `A differenza della concessione, che si limita a rimuovere un ostacolo all'esercizio di un diritto potenziale, l'autorizzazione ha natura costitutiva, in quanto attribuisce al privato un diritto di cui prima non era titolare.`, `Sia nella concessione che nell'autorizzazione vi è un potere di ingerenza della P.A. nello svolgimento del servizio che si esprime nel potere di direzione, sostituzione e revoca.` ] , 
      "0504" : [ `Indicare quale affermazione afferente le distinzioni che intercorrono tra autorizzazione e concessione e corretta.`, `Nella concessione vi è un potere di ingerenza della P.A. nello svolgimento del servizio che si esprime nel potere di direzione, sostituzione e revoca, mentre nell'autorizzazione la P.A. mantiene solo un potere di vigilanza sullo svolgimento dell'attività a seguito del quale può adottare solo provvedimenti sanzionatori.`, `Sia la concessione che l'autorizzazione hanno natura costitutiva, in quanto attribuiscono al privato un diritto di cui prima non era titolare.`, `Nell'autorizzazione vi è un potere di ingerenza della P.A. nello svolgimento del servizio che si esprime nel potere di direzione, sostituzione e revoca, mentre nella concessione la P.A. mantiene solo un potere di vigilanza sullo svolgimento dell'attività` ] , 
      "0505" : [ `Indicare quale affermazione afferente le distinzioni che intercorrono tra autorizzazione e concessione e corretta.`, `Nel rapporto concessorio l'amministrazione attribuisce al terzo il potere di svolgere una determinata attività in via esclusiva, assegnandogli una posizione di monopolio od oligopolio che si fonda su potere si riserva del bene o dell'attività all'amministrazione, con l'autorizzazione il privato opera sul mercato in concorrenza con altri soggetti autorizzati.`, `A differenza della concessione, che si limita a rimuovere un ostacolo all'esercizio di un diritto potenziale, l'autorizzazione ha natura costitutiva, in quanto attribuisce al privato un diritto di cui prima non era titolare.`, `Sia nella concessione che nell'autorizzazione la P.A. mantiene solo un potere di vigilanza sullo svolgimento dell'attività a seguito del quale può adottare solo provvedimenti sanzionatori.` ] , 
      "0506" : [ `La concessione è un atto ampliativo della sfera giuridica del destinatario che attribuisce al destinatario il diritto-dovere di svolgere in via esclusiva un'attività o di godere di un determinato bene riservati originariamente alla P.A. È un provvedimento analogo alla concessione:`, `La sovvenzione.`, `Il nulla-osta.`, `La licenza.` ] , 
      "0507" : [ `La concessione è un atto ampliativo della sfera giuridica del destinatario che attribuisce al destinatario il diritto-dovere di svolgere in via esclusiva un'attività o di godere di un determinato bene riservati originariamente alla P.A. È un provvedimento analogo alla concessione:`, `L'ammissione.`, `L'abilitazione.`, `Il nulla-osta.` ] , 
      "0508" : [ `Le concessioni:`, `Sono suscettibili di revoca nel caso in cui non corrispondano più alle esigenze pubbliche a seguito o di provvedimenti motivati o di una nuova valutazione dell'interesse pubblico.`, `Rimuovono un limite legale all'esercizio di un diritto già preesistente nella sfera giuridica del destinatario.`, `Sono provvedimenti con cui la p.a., nell'esercizio di una attività discrezionale tecnica in funzione preventiva, provvede alla rimozione di un limite legale che si frappone all'esercizio di un'attività inerente ad un diritto soggettivo.` ] , 
