      "0525" : [ `La concessione è il provvedimento amministrativo con il quale la pubblica amministrazione attribuisce diritti. Con riferimento ai poteri della p.a. è corretto affermare che:`, `La pubblica amministrazione gode del potere sostitutivo.`, `La pubblica amministrazione può solo incidere su significative facoltà decisionali del concessionario, ma non gode di poteri ispettivi.`, `La pubblica amministrazione non ha poteri di controllo e vigilanza.` ] , 
