      "0721" : [ `Oltre al principio di trasparenza, indicare quali sono gli altri quattro principi/criteri che reggono l'attività amministrativa (comma 1, art. 1, l. n. 241/1990).`, `Economicità, imparzialità, pubblicità, efficacia.`, `Economicità, giusto procedimento, pubblicità, imparzialità.`, `Economicità, precauzione, imparzialità, logica.` ] , 
