      "0797" : [ `La pubblica amministrazione deve motivare gli atti amministrativi a contenuto generale?`, `No, la motivazione per tali atti è espressamente esclusa all'art. 3 della l. n. 241/1990.`, `Si, perché producono effetti nei confronti di terzi.`, `No, la motivazione è richiesta nei soli casi espressamente previsti dalla legge.` ] , 
      "0798" : [ `"La generalizzazione dell'obbligo di motivazione dei provvedimenti amministrativi costituisce una delle principali novità della l. n.241/1990. Ai sensi dell'art. 3 l'obbligo è espressamente escluso:"`, `Per gli atti a contenuto generale.`, `Per gli atti concernenti l'organizzazione amministrativa.`, `Per gli atti discrezionali.` ] , 
      "0799" : [ `La legge n. 241/1990 ha sancito l'obbligo generale di motivazione dei provvedimenti amministrativi. Tale obbligo tuttavia non sussiste, tra l'altro,:`, `Per gli atti a contenuto generale.`, `Per gli atti privi di contenuto economico.`, `Per gli atti concernenti il personale.` ] , 
