      "0801" : [ `I regolamenti, in quanto atti amministrativi generali a contenuto normativo, necessitano di specifica motivazione in ordine alle ragioni che hanno portato a dare determinati contenuti e non altri alle proprie disposizioni?`, `No. L'obbligo di motivazione è escluso dalla legge n. 241/1990.`, `Si. Come tutti gli atti amministrativi, i regolamenti sono soggetti all'obbligo di motivazione.`, `Si. L'obbligo di motivazione è espressamente previsto dalla legge n. 241/1990.` ] , 
