      "0824" : [ `Indicare quale affermazione è conforme a quanto dispone l'art. 3bis della l. n. 241/1990 introdotto dalla l. n. 15/2005 con riferimento all'uso della telematica.`, `Le pubbliche amministrazioni incentivano l'uso della telematica per conseguire maggiore efficienza nella loro attività.`, `Le pubbliche amministrazioni incentivano l'uso della telematica nei rapporti tra le diverse amministrazioni ai soli fini di garantire l'accesso agli atti in fase preparatoria.`, `Le pubbliche amministrazioni incentivano l'uso della telematica al fine di assicurare nella fase istruttoria il libero accesso agli atti.` ] , 
      "0825" : [ `L'art. 3-bis della l. n. 241/1990, prevede che le amministrazioni pubbliche incentivano l'uso della telematica, al fine di conseguire maggiore efficienza nella loro attività:`, `Nei rapporti interni, tra le diverse amministrazioni e tra queste e i privati.`, `Solo nei rapporti tra diverse amministrazioni.`, `Solo nei rapporti con i privati.` ] , 
      "0826" : [ `L'art. 3-bis della l. n. 241/1990 prevede che le amministrazioni pubbliche incentivano l'uso della telematica, nei rapporti interni, tra le diverse amministrazioni e tra queste e i privati al fine di conseguire:`, `Maggiore efficienza nella loro attività.`, `Maggiore economicità per la loro attività.`, `Pari opportunità.` ] , 
      "0827" : [ `Il Capo I della l n. 241/1990 detta i principi generali in materia di procedimento amministrativo, stabilendo in particolare all'art. 3-bis:`, `"Che per conseguire maggiore efficienza nella loro attività, le amministrazioni pubbliche incentivano l'uso della telematica, nei rapporti interni, tra le diverse amministrazioni etra queste e i privati."`, `Che le amministrazioni pubbliche incentivano l'uso della telematica al solo fine di ridurre i costi di conservazione dei documenti.`, `Che le amministrazioni pubbliche incentivano l'uso della telematica solo nelle procedure afferenti appalti di servizi, lavori e forniture nonché concessioni.` ] , 
      "0828" : [ `Ai sensi della dell'art. 4 della legge n. 241/1990, le pubbliche amministrazioni sono tenute a determinare:`, `L'unità organizzativa responsabile di ogni adempimento procedimentale.`, `Il termine entro cui impugnare il provvedimento.`, `Solo il dirigente dell'unità organizzativa.` ] , 
