      "1311" : [ `Con riferimento al riesame con esito demolitorio del provvedimento adottato, la revoca:`, `È un provvedimento che produce effetti non retroattivi (opera ex nunc).`, `Può essere disposta anche per i provvedimenti che hanno consumato il potere e per i negozi giuridici conclusi dall'Amministrazione.`, `Rimuove ex tunc gli effetti dell'atto.` ] , 
      "1312" : [ `Con riferimento al riesame con esito demolitorio del provvedimento adottato, la revoca:`, `Ha ad oggetto provvedimenti amministrativi discrezionali con efficacia durevole.`, `Ricorre quando viene ritirato, con efficacia retroattiva, un atto amministrativo illegittimo.`, `É un provvedimento di secondo grado con cui la P.A. riconosce e rimuove un vizio che inficia un proprio provvedimento.` ] , 
      "1313" : [ `Sono suscettibili di revoca i provvedimenti ad effetti istantanei?`, `No. La revoca ha ad oggetto provvedimenti amministrativi discrezionali con efficacia durevole.`, `Si, tutti i provvedimenti possono essere revocati.`, `Si, se il provvedimento è illegittimo.` ] , 
      "1314" : [ `Sono suscettibili di revoca i provvedimenti interamente eseguiti?`, `No. La revoca ha ad oggetto provvedimenti amministrativi discrezionali con efficacia durevole.`, `Si, tutti i provvedimenti possono essere revocati.`, `Si, se il provvedimento è illegittimo.` ] , 
      "1315" : [ `Sono suscettibili di revoca i provvedimenti che hanno consumato il potere in capo all'autorità emanante?`, `No. La revoca ha ad oggetto provvedimenti amministrativi discrezionali con efficacia durevole.`, `Si, tutti i provvedimenti possono essere revocati.`, `Si, se il provvedimento è illegittimo.` ] , 
      "1316" : [ `Sono suscettibili di revoca i negozi giuridici conclusi dall'Amministrazione?`, `No. La revoca ha ad oggetto provvedimenti amministrativi discrezionali con efficacia durevole.`, `Si, tutti i provvedimenti possono essere revocati, fatta eccezione per quelli che hanno consumato il potere.`, `Si, se il provvedimento è illegittimo.` ] , 
      "1317" : [ `Quando l'Amministrazione ritira, con efficacia non retroattiva, un atto inficiato da vizi di merito in base ad una nuova valutazione degli interessi, pone in essere:`, `Un provvedimento di revoca.`, `Un provvedimento di mero ritiro.`, `Un provvedimento di rettifica.` ] , 
      "1318" : [ `"Con riferimento al riesame con esito demolitorio del provvedimento adottato, l'annullamento d'ufficio (art. 21-nonies della l. n.241/1990):"`, `Ha efficacia retroattiva.`, `Ricorre quando si differisce il termine di efficacia di un precedente provvedimento.`, `Ricorre quando l'Amministrazione ritira un atto non ancora efficace.` ] , 
      "1319" : [ `"Con riferimento al riesame con esito demolitorio del provvedimento adottato, l'annullamento d'ufficio (art. 21-nonies della l. n.241/1990):"`, `Rimuove ex tunc gli effetti dell'atto.`, `É un provvedimento nuovo, autonomo, costitutivo, che elimina i vizi di legittimità di un atto invalido precedentemente emanato dalla stessa autorità.`, `Ricorre quando viene ritirato un atto amministrativo non ancora efficace.` ] , 
