      "1322" : [ `Il riesame degli atti amministrativi può concludersi con esito demolitorio, conservativo o confermativo. Quale tra i seguenti è un riesame del provvedimento amministrativo con esito conservativo?`, `Sanatoria.`, `Conferma.`, `Sospensione dell'efficacia.` ] , 
