      "1324" : [ `Il riesame degli atti amministrativi può concludersi con esito demolitorio, conservativo o confermativo. Quale tra i seguenti è un riesame del provvedimento amministrativo con esito conservativo?`, `Riforma.`, `Sospensione dell'efficacia.`, `Revoca.` ] , 
      "1325" : [ `Il riesame degli atti amministrativi può concludersi con esito demolitorio, conservativo o confermativo. Quale tra i seguenti è un riesame del provvedimento amministrativo con esito conservativo?`, `Convalida.`, `Revoca.`, `Sospensione dell'efficacia.` ] , 
      "1326" : [ `Il riesame degli atti amministrativi può concludersi con esito demolitorio, conservativo o confermativo. Quale tra i seguenti è un riesame del provvedimento amministrativo con esito conservativo?`, `Rinnovazione.`, `Sospensione dell'efficacia.`, `Conferma.` ] , 
