      "1469" : [ `Il Codice del processo amministrativo (D.Lgs. 104/2010) di quanti libri si compone?`, `Cinque libri.`, `Quattro libri.`, `Tre libri.` ] , 
      "1470" : [ `Il Codice del processo amministrativo (D.Lgs. 104/2010) si compone:`, `"Di cinque libri (nell’ordine): disposizioni generali; processo amministrativo di primo grado; impugnazioni; ottemperanza e riti speciali; norme finali."`, `"Di quattro libri (nell’ordine): impugnazioni; processo amministrativo di primo grado; ottemperanza e riti speciali; norme finali."`, `"Di quattro libri (nell’ordine): disposizioni generali; impugnazioni; processo amministrativo di primo grado; norme finali."` ] , 
