      "1472" : [ `I Tribunali amministrativi regionali (art. 5 D.Lgs. 104/2010 - Codice del processo amministrativo):`, `Sono organi di giurisdizione amministrativa di primo grado.`, `Sono organi di giurisdizione amministrativa di secondo grado.`, `Sono organi di controllo sugli atti delle Regioni e degli enti locali.` ] , 
