      "1476" : [ `Il Codice del processo amministrativo prevede casi in cui le parti possono stare in giudizio personalmente senza l’assistenza del difensore?`, `Si, le parti possono stare in giudizio personalmente senza l’assistenza del difensore, tra l’altro nei giudizi in materia di accesso e trasparenza amministrativa.`, `No, le parti non possono stare in giudizio personalmente senza l’assistenza del difensore.`, `Si, le parti possono sempre stare in giudizio personalmente senza l’assistenza del difensore.` ] , 
