      "1882" : [ `I beni del patrimonio disponibile dello Stato e degli enti pubblici sono assoggettabili ad espropriazione?`, `"Si, in quanto il regime giuridico è quello dei beni di diritto privato “tout court”."`, `No, in quanto il regime giuridico è quello dei beni demaniali.`, `No, in quanto il regime giuridico è dello dei beni del patrimonio indisponibile.` ] , 
