      "1886" : [ `Sono disponibili tutti i beni di proprietà dello Stato e degli enti pubblici che siano diversi da quelli demaniali ed indisponibili. Quali tra i seguenti sono beni disponibili?`, `Diritti reali su cose altrui.`, `Raccolte delle pinacoteche.`, `Caserme.` ] , 
