      "1890" : [ `Con riferimento ai beni di proprietà dello Stato e degli enti pubblici, i titolo di credito sono:`, `Beni del patrimonio disponibile.`, `Beni del patrimonio indisponibile per destinazione.`, `Beni del patrimonio indisponibile per natura.` ] , 
      "1891" : [ `Con riferimento ai beni di proprietà dello Stato e degli enti pubblici, il denaro è:`, `Bene del patrimonio disponibile.`, `Bene del patrimonio indisponibile per destinazione.`, `Bene del patrimonio indisponibile per natura.` ] , 
      "1892" : [ `Con riferimento ai beni di proprietà dello Stato e degli enti pubblici, i diritti reali su cose altrui sono:`, `Beni del patrimonio disponibile.`, `Beni del patrimonio indisponibile per destinazione.`, `Beni del patrimonio indisponibile per natura.` ] , 
      "1893" : [ `Con riferimento ai beni di proprietà dello Stato e degli enti pubblici, i diritti di credito sono:`, `Beni del patrimonio disponibile.`, `Beni del patrimonio indisponibile per destinazione.`, `Beni del patrimonio indisponibile per natura.` ] , 
      "1894" : [ `Con riferimento ai beni di proprietà dello Stato e degli enti pubblici, le azioni sono:`, `Beni del patrimonio disponibile.`, `Beni del patrimonio indisponibile per destinazione.`, `Beni del patrimonio indisponibile per natura.` ] , 
      "1895" : [ `Con riferimento ai beni di proprietà dello Stato e degli enti pubblici, i titoli di Stato sono:`, `Beni del patrimonio disponibile.`, `Beni del patrimonio indisponibile per destinazione.`, `Beni del patrimonio indisponibile per natura.` ] , 
      "1896" : [ `Il patrimonio disponibile dello Stato e degli enti pubblici si distingue in quattro categorie: beni corporali, beni incorporali, titoli di credito, denaro. Quali tra i seguenti sono beni corporali?`, `Immobili, in genere.`, `Azioni.`, `Diritti reali su cose.` ] , 
      "1897" : [ `Il patrimonio disponibile dello Stato e degli enti pubblici si distingue in quattro categorie: beni corporali, beni incorporali, titoli di credito, denaro. Quali tra i seguenti sono beni incorporali?`, `Diritti reali su cose altrui.`, `Immobili, in genere.`, `Azioni.` ] , 
      "1898" : [ `Il patrimonio disponibile dello Stato e degli enti pubblici si distingue in quattro categorie: beni corporali, beni incorporali, titoli di credito, denaro. Quali tra i seguenti sono beni incorporali?`, `Diritti di credito.`, `Immobili, in genere.`, `Titoli di Stato.` ] , 
      "1899" : [ `Nell'Amministrazione comunale come sono stabilite le attribuzioni degli organi?`, `Con lo Statuto comunale.`, `Con provvedimenti adottati dalla Giunta.`, `Con ordinanze adottate dal Sindaco.` ] , 
      "1900" : [ `Nell'Amministrazione comunale come sono stabilite le forme di garanzia e di partecipazione delle minoranze?`, `Con lo Statuto comunale.`, `Con provvedimenti adottati dalla Giunta.`, `Con atti del Segretario generale.` ] , 
      "1901" : [ `Nell'Amministrazione comunale come sono stabiliti i modi di esercizio della rappresentanza legale dell'ente, anche in giudizio?`, `Con lo Statuto comunale.`, `Con ordinanze adottate dal Sindaco.`, `Con atti del Segretario generale.` ] , 
      "1902" : [ `Nell'Amministrazione comunale come sono stabiliti i criteri generali in materia di organizzazione dell'ente?`, `Con lo Statuto comunale.`, `Con legge regionale.`, `Con ordinanze adottate dal Sindaco.` ] , 
      "1903" : [ `Nell'Amministrazione comunale come sono stabilite le forme della partecipazione popolare?`, `Con lo Statuto comunale.`, `Con provvedimenti adottati dalla Giunta.`, `Con legge dello Stato.` ] , 
      "1904" : [ `Nell'Amministrazione comunale come sono stabilite le forme del decentramento?`, `Con lo Statuto comunale.`, `Con provvedimenti adottati dalla Giunta.`, `Con ordinanze adottate dal Sindaco.` ] , 
      "1905" : [ `Nell'Amministrazione comunale come sono stabilite le modalità di accesso dei cittadini alle informazioni ed ai procedimenti amministrativi?`, `Con lo Statuto comunale.`, `Con ordinanze adottate dal Sindaco.`, `Con atti del Segretario generale.` ] , 
