      "1907" : [ `Le Amministrazioni comunali possono adottare regolamenti per l'organizzazione e funzionamento delle istituzioni?`, `Si, lo prevede espressamente la legislazione vigente.`, `No, la materia è riservata allo Statuto.`, `No, la materia è riservata alla legge regionale.` ] , 
