      "1940" : [ `"L'espressione “organi di governo” contenuta dall'art. 36 del Testo unico delle leggi sull’ordinamento degli enti locali è riferita per le amministrazioni comunali, oltre al Consiglio a:"`, `Giunta e Sindaco.`, `Sindaco e Collegio dei revisori.`, `Giunta e Dirigenti.` ] , 
