      "2004" : [ `Il Sindaco oltre ad essere il Capo dell'amministrazione comunale è organo dello Stato preposto all'esercizio di funzioni di interesse statale. Indicare quale funzione è svolta dal Sindaco come capo dell'Amministrazione comunale.`, `In caso di emergenze sanitarie o di igiene pubblica a carattere esclusivamente locale, emana ordinanze contingibili e urgenti.`, `Sovrintende allo svolgimento delle funzioni affidategli dalla legge in materia di pubblica sicurezza e di polizia giudiziaria.`, `Sovrintende agli adempimenti demandatigli dalla legge in materia di statistica.` ] , 
