      "2006" : [ `Il Sindaco oltre ad essere il Capo dell'amministrazione comunale è organo dello Stato preposto all'esercizio di funzioni di interesse statale. Indicare quale funzione è svolta dal Sindaco come capo dell'Amministrazione comunale.`, `Coordina e riorganizza, sulla base degli indirizzi espressi dal Consiglio e nell'ambito dei criteri eventualmente indicati dalla Regione, gli orari degli esercizi commerciali, dei pubblici esercizi e dei servizi pubblici.`, `Sovrintende all'emanazione degli atti che gli sono attribuiti dalla legge e dai regolamenti in materia di ordine e sicurezza pubblica.`, `Adotta, con atto motivato, provvedimenti contingibili e urgenti al fine di prevenire e di eliminare gravi pericoli che minacciano l'incolumità pubblica e la sicurezza pubblica.` ] , 
      "2007" : [ `Il Sindaco oltre ad essere il Capo dell'amministrazione comunale è organo dello Stato preposto all'esercizio di funzioni di interesse statale. Indicare quale funzione è svolta dal Sindaco come capo dell'Amministrazione comunale.`, `Presiede la Giunta.`, `Sovrintende all'emanazione degli atti che gli sono attribuiti dalla legge e dai regolamenti in materia di sicurezza pubblica.`, `Provvede alla tenuta dei registri di stato civile ed al rilascio delle relative certificazioni.` ] , 
      "2008" : [ `Sindaco-Ufficiale di Governo e Sindaco-Capo dell'Amministrazione. Indicare quale funzione è svolta dal Sindaco come capo dell'Amministrazione comunale.`, `In caso di emergenze sanitarie o di igiene pubblica adottare i provvedimenti d'urgenza.`, `Sovrintende alla vigilanza su tutto quanto possa interessare la sicurezza e l'ordine pubblico, informandone il Prefetto.`, `Concorre ad assicurare anche la cooperazione della Polizia locale con le Forze di Polizia statali, nell'ambito delle direttive di coordinamento impartite dal Ministro dell'Interno.` ] , 
      "2009" : [ `Il Sindaco è eletto tra i componenti della Giunta comunale. Quanto affermato è corretto?`, `Errato, è eletto dai cittadini a suffragio universale e diretto.`, `Errato, è eletto dal Consiglio comunale con voto segreto.`, `Si.` ] , 
      "2010" : [ `Nei Comuni con più di 15.000 abitanti il Sindaco è eletto:`, `Contestualmente al Consiglio comunale.`, `Dopo aver eletto il Consiglio comunale.`, `Contestualmente alla Giunta comunale.` ] , 
      "2011" : [ `Nei comuni con popolazione superiore a 15.000 abitanti, il Sindaco:`, `È eletto a suffragio universale e diretto, contestualmente all'elezione del Consiglio comunale.`, `È eletto a suffragio universale e indiretto, contestualmente all'elezione del Consiglio comunale.`, `È eletto a suffragio universale e diretto, dopo l'elezione del Consiglio comunale.` ] , 
      "2012" : [ `A seguito dell'elezioni alla carica di Sindaco risulta eletto al primo turno:`, `Il candidato che ha ottenuto la maggioranza assoluta dei voti validi.`, `Il candidato che ha ottenuto la maggioranza dei voti validi.`, `Il candidato che ha ottenuto almeno il 40% dei voti validi.` ] , 
