      "2346" : [ `I regolamenti di organizzazione(art. 17, comma 1, lett. d), l.n. 400/1988):`, `Sono volti a disciplinare l'organizzazione e il funzionamento delle pubbliche amministrazioni secondo disposizioni dettate dalla legge.`, `Sono volti a disciplinare materie in cui l'intervento di norme primarie non si sia ancora configurato, purché non si tratti di materie soggette a riserva assoluta o relativa di legge.`, `Sono volti a completare la trama di principi fissati da leggi e decreti legislativi.` ] , 
      "2347" : [ `I regolamenti di riordino (art. 17, comma 4-ter, l.n. 400/1988):`, `Sono volti a prevedere il periodico riordino delle disposizioni regolamentari vigenti.`, `Sono emanati dal Governo in materie non soggette a riserva assoluta di legge, volti a sostituire la legge vigente.`, `Sono volti a disciplinare il funzionamento delle pubbliche amministrazioni secondo disposizioni dettate dalla legge.` ] , 
      "2348" : [ `In base all'art. 17, l. n. 400/1988, i regolamenti si distinguono in regolamenti: di esecuzione, di attuazione e integrazione, indipendenti, di organizzazione, di delegificazione, di riordino. Quali in particolare sono destinati a specificare una disciplina di rango legislativo con norme di dettaglio?`, `I regolamenti di esecuzione.`, `I regolamenti di attuazione e di integrazione.`, `I regolamenti indipendenti.` ] , 
      "2349" : [ `In base all'art. 17, l. n. 400/1988, i regolamenti si distinguono in regolamenti: di esecuzione, di attuazione e integrazione, indipendenti, di organizzazione, di delegificazione, di riordino. Quali in particolare sono gli unici ammessi ad operare nell'ambito di una riserva assoluta di legge?`, `I regolamenti di esecuzione.`, `I regolamenti indipendenti.`, `I regolamenti di riordino.` ] , 
      "2350" : [ `In base all'art. 17, l. n. 400/1988, i regolamenti si distinguono in regolamenti: di esecuzione, di attuazione e integrazione, indipendenti, di organizzazione, di delegificazione, di riordino. Quali in particolare sono volti a completare la trama di principi fissati da leggi e decreti legislativi recanti norme di principio?`, `I regolamenti di attuazione e di integrazione.`, `I regolamenti indipendenti.`, `I regolamenti di riordino.` ] , 
      "2351" : [ `In base all'art. 17, l. n. 400/1988, i regolamenti si distinguono in regolamenti: di esecuzione, di attuazione e integrazione, indipendenti, di organizzazione, di delegificazione, di riordino. Quali in particolare sono volti a disciplinare materie in cui l'intervento di norme primarie non si sia ancora configurato, purché non si tratti di materie soggette a riserva assoluta o relativa di legge?`, `I regolamenti indipendenti.`, `I regolamenti di attuazione e di integrazione.`, `I regolamenti di organizzazione.` ] , 
      "2352" : [ `In base all'art. 17, l. n. 400/1988, i regolamenti si distinguono in regolamenti: di esecuzione, di attuazione e integrazione, indipendenti, di organizzazione, di delegificazione, di riordino. Quali in particolare sono volti a disciplinare l'organizzazione e il funzionamento delle pubbliche amministrazioni secondo disposizioni dettate dalla legge?`, `I regolamenti di organizzazione.`, `I regolamenti indipendenti.`, `I regolamenti di riordino.` ] , 
      "2353" : [ `In base all'art. 17, l. n. 400/1988, i regolamenti si distinguono in regolamenti: di esecuzione, di attuazione e integrazione, indipendenti, di organizzazione, di delegificazione, di riordino. Quali in particolare sono volti a prevedere il periodico riordino delle disposizioni regolamentari vigenti?`, `I regolamenti di riordino.`, `I regolamenti di esecuzione.`, `I regolamenti di attuazione e di integrazione.` ] , 
