      "2377" : [ `Il Piano della performance di cui all'art. 10 del D.Lgs. n. 150/2009:`, `É adottato entro il 31 gennaio.`, `É adottato entro il 1° gennaio.`, `É adottato entro il 20 febbraio.` ] , 
      "2378" : [ `Qual è la valenza del Piano della performance di cui all'art. 10 del D.Lgs. n. 150/2009?`, `Triennale.`, `Biennale.`, `Quinquennale.` ] , 
      "2379" : [ `Il Piano della performance di cui all'art. 10 del D.Lgs. n. 150/2009:`, `È un documento programmatico triennale.`, `È un documento consuntivo.`, `È un documento di indirizzo quinquennale.` ] , 
      "2380" : [ `Il Piano della performance di cui all'art. 10 del D.Lgs. n. 150/2009:`, `É adottato in coerenza con i contenuti e il ciclo della programmazione finanziaria e di bilancio.`, `"Evidenzia, a consuntivo i risultati organizzativi e individuali raggiunti rispetto ai singoli obiettivi programmati ed alle risorse, con rilevazione degli eventuali scostamenti, e ilbilancio di genere realizzato."`, `È un documento programmatico annuale.` ] , 
      "2381" : [ `Il Piano della performance di cui all'art. 10 del D.Lgs. n. 150/2009:`, `Individua gli indirizzi e gli obiettivi strategici ed operativi.`, `Evidenzia, a consuntivo, con riferimento all'anno precedente, i risultati organizzativi e individuali raggiunti rispetto ai singoli obiettivi programmati ed alle risorse.`, `È un documento di indirizzo quinquennale.` ] , 
      "2382" : [ `La Relazione sulla performance di cui all'art. 10 del D.Lgs. n. 150/2009:`, `É adottata entro il 30 giugno.`, `É adottata entro il 31 gennaio.`, `É adottata entro il 20 febbraio.` ] , 
      "2383" : [ `La Relazione sulla performance di cui all'art. 10 del D.Lgs. n. 150/2009:`, `È un documento consuntivo riferito all'anno precedente.`, `È un documento consuntivo riferito all'anno in corso.`, `É un documento programmatico triennale.` ] , 
      "2384" : [ `La Relazione sulla performance di cui all'art. 10 del D.Lgs. n. 150/2009:`, `Evidenzia, a consuntivo, con riferimento all'anno precedente, i risultati organizzativi e individuali raggiunti rispetto ai singoli obiettivi programmati ed alle risorse, con rilevazione degli eventuali scostamenti, e il bilancio di genere realizzato.`, `Individua gli indirizzi e gli obiettivi strategici ed operativi.`, `Definisce gli obiettivi assegnati al personale dirigenziale ed i relativi indicatori.` ] , 
      "2385" : [ `La Relazione sulla performance di cui all'art. 10 del D.Lgs. n. 150/2009:`, `Evidenzia i risultati organizzativi e individuali raggiunti rispetto ai singoli obiettivi programmati ed alle risorse, con rilevazione degli eventuali scostamenti, e il bilancio di genere realizzato.`, `Individua gli indirizzi e gli obiettivi strategici ed operativi e definisce, con riferimento agli obiettivi finali ed intermedi ed alle risorse, gli indicatori per la misurazione e la valutazione della performance dell'amministrazione, nonché gli obiettivi assegnati al personale dirigenziale ed i relativi indicatori.`, `È un documento programmatico triennale.` ] , 
      "2386" : [ `Eventuali variazioni durante l'esercizio degli obiettivi della performance organizzativa e individuale sono inserite all'interno del Piano della performance?`, `Si, lo prevede espressamente l'art. 10 del D.Lgs. n. 150/2009.`, `Non necessariamente.`, `No, il Piano della performance è un documento programmatico triennale. Eventuali variazioni saranno inserire nella Relazione della performance.` ] , 
      "2387" : [ `Eventuali variazioni durante l'esercizio degli indicatori della performance organizzativa e individuale sono inserite all'interno del Piano della performance?`, `Si, lo prevede espressamente l'art. 10 del D.Lgs. n. 150/2009.`, `Solo le variazioni che risultano essere di notevole entità.`, `No, il Piano della performance è un documento programmatico triennale. Eventuali variazioni saranno inserire nella Relazione della performance.` ] , 
      "2388" : [ `Qualora una amministrazione pubblica non adotti il Piano della performance può procedere ad assunzioni di personale?`, `No, lo vieta espressamente l'art. 10 del D.Lgs. n. 150/2009.`, `È in facoltà dell'amministrazione procedere o meno ad assunzioni di personale.`, `No, ma può procedere al conferimento di incarichi di consulenza o di collaborazione comunque denominati.` ] , 
      "2389" : [ `Qualora una amministrazione pubblica non adotti il Piano della performance può procedere al conferimento di incarichi di consulenza o di collaborazione comunque denominati?`, `No, lo vieta espressamente l'art. 10 del D.Lgs. n. 150/2009.`, `È in facoltà dell'amministrazione procedere o meno al conferimento di incarichi di consulenza o di collaborazione comunque denominati.`, `Si, la mancata adozione del Piano delle performance comporta solo responsabilità dirigenziale.` ] , 
      "2390" : [ `"Nell'ambito del processo di misurazione e valutazione della performance organizzativa ed individuale, intervengono soggetti con ruoli e attività diverse. A quale soggetto il comma 4, art. 14, D.Lgs. 150/2009 attribuisce il compito di monitorare il funzionamento complessivo del sistema della valutazione, della trasparenza e integrità dei controlli interni ed elaborare una relazione annuale sullostato dello stesso?"`, `OIV.`, `Dipartimento della Ragioneria Generale dello Stato.`, `Dipartimento della Funzione Pubblica.` ] , 
