      "2392" : [ `Nell'ambito del processo di misurazione e valutazione della performance organizzativa ed individuale, intervengono soggetti con ruoli e attività diverse. A quale soggetto il comma 4, art. 14, D.Lgs. 150/2009 attribuisce il compito di validare la Relazione sulla performance e assicurarne la visibilità attraverso la pubblicazione sul sito istituzionale dell'amministrazione?`, `OIV.`, `Dipartimento della Ragioneria Generale dello Stato.`, `Dipartimento della Funzione Pubblica.` ] , 
      "2393" : [ `"Nell'ambito del processo di misurazione e valutazione della performance organizzativa ed individuale, intervengono soggetti con ruoli e attività diverse. A quale soggetto il comma 4, art. 14, D.Lgs. 150/2009 attribuisce il compito di garantire la correttezza dei processi di misurazione e valutazione, nonché dell'utilizzo dei premi di cui al Titolo III, secondo quanto previsto dalla vigente legislazione, nel rispetto del principio di valorizzazione del merito edella professionalità?"`, `OIV.`, `Dipartimento della Ragioneria Generale dello Stato.`, `Dipartimento della Funzione Pubblica.` ] , 
      "2394" : [ `Nell'ambito del processo di misurazione e valutazione della performance organizzativa ed individuale, intervengono soggetti con ruoli e attività diverse. A quale soggetto il comma 4, art. 14, D.Lgs. 150/2009 attribuisce il compito di proporre, sulla base del sistema di misurazione e valutazione (art. 7), all'organo di indirizzo politico- amministrativo, la valutazione annuale dei dirigenti di vertice e l'attribuzione ad essi dei premi di cui al Titolo III?`, `OIV.`, `Dipartimento della Ragioneria Generale dello Stato.`, `Dipartimento della Funzione Pubblica.` ] , 
      "2395" : [ `Nell'ambito del processo di misurazione e valutazione della performance organizzativa ed individuale, intervengono soggetti con ruoli e attività diverse. A quale soggetto il comma 4, art. 14, D.Lgs. 150/2009 attribuisce il compito di promuovere e attestare l'assolvimento degli obblighi relativi alla trasparenza e all'integrità di cui al Titolo II?`, `OIV.`, `Dipartimento della Ragioneria Generale dello Stato.`, `Dipartimento della Funzione Pubblica.` ] , 
      "2396" : [ `Nell'ambito del processo di misurazione e valutazione della performance organizzativa ed individuale, intervengono soggetti con ruoli e attività diverse. A quale soggetto il comma 4, art. 14, D.Lgs. 150/2009 attribuisce il compito di verificare i risultati e le buone pratiche di promozione delle pari opportunità?`, `OIV.`, `Dipartimento della Ragioneria Generale dello Stato.`, `Dipartimento della Funzione Pubblica.` ] , 
      "2397" : [ `"L’Organismo indipendente di valutazione della performance (OIV), a norma del disposto di cui al comma 4, art. 14, del D.Lgs. n.150/2009:"`, `Monitora il funzionamento complessivo del sistema della valutazione, della trasparenza e integrità dei controlli interni ed elabora una relazione annuale sullo stato dello stesso.`, `Fornisce supporto tecnico e metodologico all'attuazione delle varie fasi del ciclo di gestione della performance.`, `Definisce la struttura e le modalità di redazione del Piano della performance e della Relazione sulla performance.` ] , 
      "2398" : [ `"L’Organismo indipendente di valutazione della performance (OIV), a norma del disposto di cui al comma 4, art. 14, del D.Lgs. n.150/2009:"`, `Valida la Relazione sulla performance e ne assicura la visibilità attraverso la pubblicazione sul sito istituzionale dell'amministrazione.`, `"Definisce un programma di sostegno a progetti innovativi e sperimentali, concernenti il miglioramento della performance attraverso le funzioni di misurazione, valutazione econtrollo."`, `Predispone una relazione annuale sulla performance delle amministrazioni centrali e garantisce la diffusione attraverso la pubblicazione sul proprio sito istituzionale.` ] , 
      "2399" : [ `"L’Organismo indipendente di valutazione della performance (OIV), a norma del disposto di cui al comma 4, art. 14, del D.Lgs. n.150/2009:"`, `Garantisce la correttezza dei processi di misurazione e valutazione, nonché dell'utilizzo dei premi, nel rispetto del principio di valorizzazione del merito e della professionalità.`, `Redige la graduatoria di performance delle amministrazioni statali e degli enti pubblici nazionali.`, `"Definisce un programma di sostegno a progetti innovativi e sperimentali, concernenti il miglioramento della performance attraverso le funzioni di misurazione, valutazione econtrollo."` ] , 
      "2400" : [ `"L’Organismo indipendente di valutazione della performance (OIV), a norma del disposto di cui al comma 4, art. 14, del D.Lgs. n.150/2009:"`, `Propone, sulla base del sistema  di cui all'art. 7, all’organo di indirizzo politico-amministrativo, la valutazione annuale dei dirigenti di vertice e l'attribuzione ad essi dei premi.`, `Promuove analisi comparate della performance delle amministrazioni pubbliche sulla base di indicatori di andamento gestionale e la loro diffusione attraverso la pubblicazione nei siti istituzionali ed altre modalità ed iniziative ritenute utili.`, `Redige la graduatoria di performance delle amministrazioni statali e degli enti pubblici nazionali.` ] , 
      "2401" : [ `"L’Organismo indipendente di valutazione della performance (OIV), a norma del disposto di cui al comma 4, art. 14, del D.Lgs. n.150/2009:"`, `Promuove e attesta l'assolvimento degli obblighi relativi alla trasparenza e all'integrità di cui al Titolo II.`, `Definisce la struttura e le modalità di redazione del Piano della performance e della Relazione sulla performance.`, `"Verifica la corretta predisposizione del Piano e della Relazione sulla Performance delle amministrazioni centrali e, a campione, analizza quelli degli Enti territoriali, formulando osservazioni e specificirilievi."` ] , 
      "2402" : [ `"L’Organismo indipendente di valutazione della performance (OIV), a norma del disposto di cui al comma 4, art. 14, del D.Lgs. n.150/2009:"`, `Verifica i risultati e le buone pratiche di promozione delle pari opportunità.`, `Predispone una relazione annuale sulla performance delle amministrazioni centrali e garantisce la diffusione attraverso la pubblicazione sul proprio sito istituzionale.`, `Fornisce supporto tecnico e metodologico all'attuazione delle varie fasi del ciclo di gestione della performance.` ] , 
