      "2834" : [ `"Gli atti presidenziali possono classificarsi in tre grandi categorie, a seconda che siano: atti formalmente presidenziali e sostanzialmente governativi; atti formalmente e sostanzialmente presidenziali; atti formalmente presidenziali e sostanzialmente complessi.Appartiene/appartengono alla terza categoria di atti:"`, `Lo scioglimento delle Camere.`, `La nomina dei cinque senatori a vita.`, `La concessione della grazia.` ] , 
      "2835" : [ `"Gli atti presidenziali possono classificarsi in tre grandi categorie, a seconda che siano: atti formalmente presidenziali e sostanzialmente governativi; atti formalmente e sostanzialmente presidenziali; atti formalmente presidenziali e sostanzialmente complessi.Appartiene/appartengono alla seconda categoria:"`, `La nomina dei cinque senatori a vita.`, `La nomina del Presidente del Consiglio.`, `Lo scioglimento delle Camere.` ] , 
      "2836" : [ `"Gli atti presidenziali possono classificarsi in tre grandi categorie, a seconda che siano: atti formalmente presidenziali e sostanzialmente governativi; atti formalmente e sostanzialmente presidenziali; atti formalmente presidenziali e sostanzialmente complessi.Appartiene/appartengono alla seconda categoria:"`, `La nomina dei cinque giudici costituzionali.`, `La nomina del Presidente del Consiglio.`, `Lo scioglimento delle Camere.` ] , 
      "2837" : [ `"Gli atti presidenziali possono classificarsi in tre grandi categorie, a seconda che siano: atti formalmente presidenziali e sostanzialmente governativi; atti formalmente e sostanzialmente presidenziali; atti formalmente presidenziali e sostanzialmente complessi.Appartiene/appartengono alla terza categoria di atti:"`, `La nomina del Presidente del Consiglio.`, `La nomina dei cinque giudici costituzionali.`, `Il rinvio al Parlamento di una legge, la promulgazione delle leggi, i messaggi.` ] , 
      "2838" : [ `Se si considera, per ciascun tipo di atti del Presidente della Repubblica, il grado di autodeterminazione di cui in effetti dispone, quale dei seguenti è atto d'iniziativa presidenziale?`, `Messaggi alle Camere.`, `Autorizzazione alla ratifica dei trattati internazionali.`, `Promulgazione delle leggi.` ] , 
      "2839" : [ `Se si considera, per ciascun tipo di atti del Presidente della Repubblica, il grado di autodeterminazione di cui in effetti dispone, quale dei seguenti è atto d'iniziativa presidenziale?`, `Rinvio delle leggi operabile in sede di promulgazione.`, `Indizione dell'elezione delle nuove Camere e fissazione della prima riunione.`, `Ratifica dei trattati internazionali di natura politica.` ] , 
      "2840" : [ `In quale delle seguenti fattispecie si configura un atto d'iniziativa presidenziale?`, `Nomina dei cinque giudici costituzionali.`, `Dichiarazione dello stato di guerra.`, `Promulgazione delle leggi.` ] , 
