      "2842" : [ `Quale delle seguenti fattispecie configura un atto presidenziale giuridicamente dovuto?`, `Promulgazione delle leggi.`, `Rinvio delle leggi, operabile in sede di promulgazione.`, `Autorizzazione alla ratifica dei trattati internazionali.` ] , 
      "2843" : [ `Quale delle seguenti fattispecie configura un atto presidenziale giuridicamente dovuto?`, `Indizione dell'elezione delle nuove Camere.`, `Messaggi alle Camere.`, `Nomina dei cinque Senatori a vita.` ] , 
      "2844" : [ `Nel 2001 la legge costituzionale del 18 ottobre, n. 3,....`, `Ha apportato una modifica sostanziale al Titolo V nella Parte Seconda della Costituzione, dedicato alle Regioni, alle Province e ai Comuni.`, `Ha apportato una modifica sostanziale al Titolo I nella Parte Prima della Costituzione, dedicato ai rapporti civili.`, `Ha apportato una modifica sostanziale al Titolo III nella Parte Seconda della Costituzione, dedicato al Governo.` ] , 
      "2845" : [ `Costituiscono ripartizioni del territorio della Repubblica, ai sensi dell'art. 114 della Costituzione:`, `Comuni, Province, Città metropolitane, Regioni e Stato.`, `Regioni, Province e Comuni.`, `Regioni, Province e Città metropolitane.` ] , 
      "2846" : [ `La legge costituzionale 18 ottobre 2001, n. 3, che ha modificato il Titolo V della Costituzione, ha delineato un nuovo assetto della Repubblica italiana abbandonando l'originaria impostazione voluta dai costituenti. Tra gli aspetti più importanti della riforma va citata la diversa articolazione della Repubblica che con la nuova formulazione dell'articolo 114 si compone di:`, `Comuni, Province, Città metropolitane, Regioni e Stato.`, `Regioni, Province e loro associazioni, Comunità montane.`, `Comuni, Province, Regioni e loro federazioni.` ] , 
      "2847" : [ `"La legge costituzionale n. 3 del 2001 ha ridisegnato la piramide dei pubblici poteri preposti ad assicurare il benessere della collettività. I soggetti titolari dei poteri pubblici non sono elencati secondo un criterio di supremazia, ma in base all'effettivo rapporto con la popolazione a cui sono dirette le loro azioni; essi sono nell'ordine, indicato dall'art. 114 Cost.:"`, `Comuni, Province, Città metropolitane, Regioni.`, `Comuni, Città metropolitane, Province, Regioni.`, `Città metropolitane Comuni, Province, Regioni.` ] , 
      "2848" : [ `Con la nuova articolazione territoriale della Repubblica disegnata dalla riforma costituzionale del 2001, le città metropolitane:`, `Compongono la Repubblica al pari di Comuni, Province, Regioni e Stato.`, `Sono una formula di associazione di Province.`, `Sono un'articolazione interna di Comuni.` ] , 
      "2849" : [ `Ai sensi dell'art. 114 della Costituzione, la Repubblica è costituita:`, `Dai Comuni, dalle Province, dalle Città metropolitane, dalle Regioni e dallo Stato.`, `Dalle Province, dalle Città metropolitane e dalle Regioni.`, `Dai Comuni, dalle Città metropolitane e dallo Stato.` ] , 
      "2850" : [ `Ai sensi dell'art. 114 della Costituzione, la Repubblica è costituita, oltre che dallo Stato:`, `Dai Comuni, dalle Province, dalle Città metropolitane, dalle Regioni.`, `Dalle Province, dalle Città metropolitane e dalle Regioni.`, `Dai Comuni, dalle Città metropolitane.` ] , 
      "2851" : [ `Ai sensi dell'art. 114 della Costituzione, la Repubblica è costituita, oltre che dallo Stato e dalle Regioni:`, `Dai Comuni, dalle Province, dalle Città metropolitane.`, `Dalle Province, dalle Città metropolitane.`, `Dai Comuni, dalle Città metropolitane.` ] , 
      "2852" : [ `”"La Repubblica è costituita -----------, dalle Province, dalle Città metropolitane, dalle Regioni e dallo Stato.” Completare con l'opzione corretta."`, `Dai Comuni.`, `Dagli enti territoriali.`, `Dagli enti economici.` ] , 
      "2853" : [ `”"La Repubblica è costituita dai Comuni, -----------, dalle Città metropolitane, dalle Regioni e dallo Stato.” Completare con l'opzione corretta."`, `Dalle Province.`, `Dagli enti territoriali.`, `Dagli enti economici.` ] , 
      "2854" : [ `”"La Repubblica è costituita dai Comuni, dalle Province, -----------, dalle Regioni e dallo Stato.” Completare con l'opzione corretta."`, `Dalle Città metropolitane.`, `Dagli enti territoriali.`, `Dagli enti economici.` ] , 
