      "2864" : [ `Le Regioni a statuto speciale espressamente previste dalla Costituzione sono:`, `Sicilia, Sardegna, Trentino-Alto Adige/Sudtirol, Friuli-Venezia Giulia e Valle d'Aosta/Vallée d'Aoste.`, `Sicilia, Sardegna, Trentino-Alto Adige/Sudtirol e Valle d'Aosta/Vallée d'Aoste.`, `Sicilia, Trentino-Alto Adige/Sudtirol, Friuli-Venezia Giulia e Valle d'Aosta/Vallée d'Aoste.` ] , 
      "2865" : [ `Quante e quali sono le Regioni a statuto speciale?`, `Sono cinque: Valle d'Aosta, Trentino Alto Adige, Friuli Venezia Giulia, Sicilia, Sardegna.`, `Sono due: Valle d'Aosta e Trentino Alto Adige.`, `Sono tre: Valle d'Aosta, Trentino Alto Adige e Friuli Venezia Giulia.` ] , 
      "2866" : [ `Le Regioni italiane sono:`, `Venti: cinque a statuto speciale e quindici a statuto ordinario.`, `Venti: tre a statuto speciale e Diciassette a statuto ordinario.`, `Quindici: cinque a statuto speciale e dieci a statuto ordinario.` ] , 
      "2867" : [ `Quante sono le Regioni a statuto speciale?`, `Cinque.`, `Tre.`, `Sei.` ] , 
      "2868" : [ `Nel terzo comma dell'art. 116 della Costituzione viene introdotta nel 2001 una norma che può aprire la strada a quello che viene chiamato regionalismo differenziato, in quanto le regioni solitamente definite a statuto ordinario, potranno accedere a forme maggiori di autonomia, anche se solo nei campi indicati dalla norma. Quante sono le Regioni a Statuto ordinario?`, `15,`, `16,`, `13,` ] , 
      "2869" : [ `Con la Riforma costituzionale del 2001, non solo le regioni speciali godono di forme e condizioni particolari di autonomia ma anche le regioni a statuto ordinario possono beneficiare di maggiore autonomia, su iniziativa della regione interessata e con legge dello Stato. Quante sono le Regioni a Statuto ordinario?`, `15,`, `16,`, `17,` ] , 
