      "3112" : [ `La mozione di sfiducia nei confronti del Presidente della Giunta regionale:`, `Deve essere sottoscritta da un certo numero di componenti il Consiglio regionale e deve essere approvata per appello nominale.`, `Deve essere sottoscritta da un certo numero di componenti il Consiglio regionale ed è votata a scrutinio segreto.`, `Non è ammessa per espressa previsione costituzionale.` ] , 
