      "3244" : [ `In quale momento i Giudici della Corte costituzionale entrano nell'esercizio delle loro funzioni?`, `Quando prestano giuramento.`, `Nel giorno successivo alle elezioni.`, `Dopo cinque giorni dalla data in cui prestano giuramento.` ] , 
      "3245" : [ `Alla scadenza del mandato, i giudici costituzionali possono essere nuovamente nominati?`, `No, non possono essere nuovamente nominati.`, `Si, ma per non più di tre anni.`, `Si, possono essere nuovamente nominati.` ] , 
      "3246" : [ `Un giudice costituzionale nominato dalle supreme magistrature ordinaria ed amministrative può essere nuovamente nominato alla scadenza del novennato?`, `No. La Costituzione esclude la nomina per un secondo novennato.`, `Si. La Costituzione prevede la nomina per un secondo novennato.`, `La Costituzione esclude la nomina per un secondo novennato solo per i giudici nominati dal Presidente della Repubblica.` ] , 
      "3247" : [ `Un giudice costituzionale nominato dal Presidente della Repubblica può essere nuovamente nominato alla scadenza del novennato?`, `No, per espressa previsione costituzionale.`, `Si. La Costituzione prevede la nomina per un secondo novennato.`, `Si. La Costituzione lo prevede al fine di garantire la continuità funzionale.` ] , 
      "3248" : [ `Un giudice costituzionale nominato dal Parlamento in seduta comune può essere nuovamente nominato alla scadenza del novennato?`, `No, non può essere nuovamente nominato.`, `Si, può essere nuovamente nominato.`, `Solo i giudici nominati dal Presidente della Repubblica possono essere nominati per un secondo novennato.` ] , 
      "3249" : [ `Quale è la durata in carica dei giudici della Corte costituzionale e quale è quella del Presidente della Corte costituzionale?`, `I giudici durano in carica nove anni il Presidente tre anni.`, `Nove anni sia per i giudici sia per il Presidente.`, `Tre anni sia per i giudici sia per il Presidente.` ] , 
      "3250" : [ `Alla scadenza del mandato un giudice costituzionale nominato dal Presidente della Repubblica può essere nuovamente nominato?`, `No.`, `Si, ma da un organo diverso.`, `Si, per un'altra volta soltanto.` ] , 
      "3251" : [ `Per quanto tempo rimane in carica il Presidente della Corte costituzionale?`, `Tre anni.`, `Nove anni.`, `Cinque anni.` ] , 
      "3252" : [ `Il Presidente della Corte Costituzionale è eletto:`, `Per un triennio ed è rieleggibile.`, `Per un triennio e non è rieleggibile.`, `Per nove anni ed è rieleggibile.` ] , 
      "3253" : [ `Chi elegge il Presidente della Corte Costituzionale?`, `La stessa Corte tra i suoi membri.`, `La stessa Corte tra soggetti estranei alla Corte stessa.`, `Il Presidente della Repubblica.` ] , 
      "3254" : [ `Il Presidente della Corte costituzionale:`, `É eletto dalla Corte tra i suoi componenti.`, `É eletto dalla Corte tra i componenti di nomina del Parlamento.`, `É designato dal Parlamento.` ] , 
      "3255" : [ `Alla scadenza del mandato il Presidente della Corte costituzionale è rieleggibile?`, `Si, fermi restando i termini di scadenza dall'ufficio di giudice.`, `Si, ma solo per un altro mandato.`, `No, lo esclude espressamente l'art. 135 della Costituzione.` ] , 
      "3256" : [ `”"----------- (1) elegge tra i suoi componenti, secondo le norme stabilite dalla legge, il Presidente, che rimane in carica per ----------- (2), ed è rieleggibile, fermi in ogni caso i termini di scadenza dall'ufficio di giudice.” Completare con l'opzione corretta."`, `La Corte costituzionale - un triennio.`, `La Corte costituzionale - un novennato.`, `Il Presidente della Repubblica - un novennato.` ] , 
      "3257" : [ `”"----------- (1) elegge tra i suoi componenti, secondo le norme stabilite dalla legge, il Presidente, che rimane in carica per un triennio, ----------- (2), fermi in ogni caso i termini di scadenza dall'ufficio di giudice.” Completare con l'opzione corretta."`, `La Corte costituzionale - ed è rieleggibile.`, `La Corte costituzionale - e non è rieleggibile.`, `Il Parlamento - e non è rieleggibile.` ] , 
      "3258" : [ `”"La Corte costituzionale elegge tra ----------- (1), secondo le norme stabilite dalla legge, il Presidente, che rimane in carica per ----------- (2), ed è rieleggibile, fermi in ogni caso i termini di scadenza dall'ufficio di giudice.” Completare con l'opzione corretta."`, `I suoi componenti - un triennio.`, `I suoi componenti nominati dal Presidente della Repubblica - un triennio.`, `I suoi componenti - un biennio.` ] , 
      "3259" : [ `Ai sensi dell'art. 135 della Costituzione, l'ufficio di giudice della Corte costituzionale è:`, `Incompatibile, tra l'altro, con la carica di consigliere regionale.`, `Compatibile solo con la carica di membro del Parlamento.`, `Incompatibile con qualsiasi altra attività.` ] , 
      "3260" : [ `Ai sensi dell'art. 135 della Costituzione, l'ufficio di giudice della Corte costituzionale è:`, `Incompatibile, tra l'altro, con l'esercizio della professione di avvocato.`, `Compatibile solo con la carica di consigliere regionale.`, `Incompatibile con qualsiasi altra attività.` ] , 
      "3261" : [ `Ai sensi dell'art. 135 della Costituzione, l'ufficio di giudice della Corte costituzionale è:`, `Incompatibile, tra l'altro, con la carica di membro del Parlamento.`, `Compatibile solo con la carica di consigliere regionale.`, `Incompatibile con qualsiasi altra attività.` ] , 
