      "3264" : [ `L'importanza e la delicatezza delle funzioni che i giudici della Corte costituzionale svolgono comportano incompatibilità e prerogative. L'ufficio di giudice della Corte costituzionale è:`, `Incompatibile anche con l'esercizio di amministratore o sindaco di società commerciale con fine di lucro.`, `Compatibile solo con la carica di consigliere regionale.`, `Incompatibile anche con la semplice iscrizione ad un partito politico.` ] , 
      "3265" : [ `L'importanza e la delicatezza delle funzioni che i giudici della Corte costituzionale svolgono comportano incompatibilità e prerogative. L'ufficio di giudice della Corte costituzionale è:`, `Incompatibile anche con la presenza attiva in un partito politico e lo svolgimento di qualsiasi attività per conto di esso.`, `Compatibile solo con la carica di membro del Parlamento.`, `Incompatibile con qualsiasi altra attività.` ] , 
      "3266" : [ `A norma della Costituzione, l'ufficio di giudice della Corte costituzionale è:`, `Incompatibile, tra l'altro, con l'esercizio della professione di avvocato.`, `Compatibile solo con la carica di consigliere regionale.`, `Incompatibile con qualsiasi altra attività.` ] , 
      "3267" : [ `A norma della Costituzione, l'ufficio di giudice della Corte costituzionale è:`, `Incompatibile, tra l'altro, con la candidatura in elezioni amministrative.`, `Compatibile solo con la carica di consigliere regionale.`, `Incompatibile con qualsiasi altra attività.` ] , 
      "3268" : [ `Ai sensi dell'art. 135 della Costituzione, l'ufficio di giudice della Corte costituzionale è:`, `Incompatibile, tra l'altro, con la candidatura in elezioni politiche.`, `Compatibile solo con la carica di consigliere regionale.`, `Incompatibile con qualsiasi altra attività.` ] , 
      "3269" : [ `I giudici della Corte Costituzionale possono essere candidati in elezioni amministrative o politiche?`, `No, lo vieta espressamente la legge.`, `No, salvo espressa autorizzazione del Presidente della Corte Costituzionale.`, `Si, ma in caso di elezione devono dimettersi dall'ufficio di giudice costituzionale.` ] , 
      "3270" : [ `Ai sensi dell'art. 135 della Costituzione, l'ufficio di giudice della Corte costituzionale è:`, `Incompatibile, tra l'altro, con la carica di membro del Parlamento.`, `Compatibile solo con la carica di consigliere regionale.`, `Incompatibile con qualsiasi altra attività.` ] , 
      "3271" : [ `Un consigliere regionale può essere membro della Corte costituzionale?`, `No, l'ufficio di giudice della Corte è incompatibile anche con quello di consigliere regionale.`, `No, salva autorizzazione del Presidente della Corte stessa.`, `Si, tra i due uffici non esiste alcuna incompatibilità.` ] , 
      "3272" : [ `Dispone la Costituzione che l'ufficio di giudice della Corte costituzionale è:`, `Incompatibile, tra l'altro, con la carica di consigliere regionale.`, `Compatibile solo con la carica di membro del Parlamento.`, `Incompatibile con qualsiasi altra attività.` ] , 
      "3273" : [ `Ai sensi del Titolo VI - Garanzie Costituzionali - sulle accuse promosse contro il Presidente della Repubblica giudica:`, `La Corte costituzionale integrata dai 16 giudici aggregati o non togati.`, `Il Consiglio di Stato.`, `La Corte dei Conti.` ] , 
      "3274" : [ `Chi giudica sulle accuse promosse contro il Presidente della Repubblica a norma delle garanzie costituzionali?`, `La Corte costituzionale integrata dai 16 giudici aggregati o non togati.`, `Il Parlamento in seduta congiunta.`, `La Corte di Cassazione.` ] , 
