      "3279" : [ `Dispone la Costituzione all'art. 135, ultimo comma, che nei giudizi d'accusa contro il Presidente della Repubblica la Corte Costituzionale è integrata da sedici membri tratti a sorte da un elenco di cittadini aventi i requisiti per l'eleggibilità a senatore, che il Parlamento compila:`, `Ogni nove anni.`, `Ogni anno.`, `Ogni due anni.` ] , 
      "3280" : [ `Nei giudizi d'accusa contro il Presidente della Repubblica la Corte Costituzionale è integrata dai c.d. membri non togati tratti a sorte da un elenco che il Parlamento compila ogni nove anni. L'elenco si compone:`, `Di cittadini aventi i requisiti per l'eleggibilità a senatore.`, `Di giudici ordinari con esperienza almeno triennale.`, `Di giudici membri del Parlamento.` ] , 
      "3281" : [ `A norma della Costituzione, quando cessa di avere efficacia una legge dello Stato dichiarata illegittima dalla Corte Costituzionale?`, `Dal giorno successivo alla pubblicazione della decisione.`, `Dal giorno della pronuncia della sentenza d'illegittimità.`, `Dal giorno in cui è stata sollevata la questione d'illegittimità.` ] , 
