      "3283" : [ `Quando la Corte costituzionale dichiara l'illegittimità costituzionale di una norma di legge o di un atto avente forza di legge, la norma:`, `Cessa di avere efficacia dal giorno successivo alla pubblicazione della decisione.`, `Cessa di avere efficacia dal giorno di dichiarazione dell'illegittimità.`, `Cessa di avere efficacia dal quindicesimo giorno successivo alla pubblicazione della decisione.` ] , 
