      "3286" : [ `A norma delle disposizioni contenute nel primo comma dell'art. 137 della Costituzione, come sono stabilite le condizioni, le forme e i termini di proponibilità dei giudizi di legittimità costituzionale?`, `Sono stabilite da una legge costituzionale.`, `Sono stabilite da una apposita Commissione parlamentare.`, `Sono stabilite da una legge ordinaria della Repubblica.` ] , 
      "3287" : [ `A norma delle disposizioni contenute nel primo comma dell'art. 137 della Costituzione, come sono stabilite le garanzie d'indipendenza dei giudici della Corte costituzionale?`, `Sono stabilite da una legge costituzionale.`, `Sono stabilite con decreto-legge.`, `Sono stabilite dalla stessa Corte costituzionale.` ] , 
