      "3289" : [ `A norma del dettato costituzionale le norme che disciplinano la costituzione e il funzionamento della Corte costituzionale sono stabilite:`, `Con legge ordinaria della Repubblica.`, `Con legge costituzionale.`, `Dalla Costituzione stessa.` ] , 
