      "3292" : [ `Il referendum di cui all'art. 138 della Costituzione:`, `Può avere per oggetto una legge costituzionale.`, `Può avere per oggetto l'abrogazione parziale di una legge dello Stato.`, `Può avere per oggetto l'abrogazione totale di un atto avente forza di legge dello Stato.` ] , 
