      "3302" : [ `Dispone la Costituzione che le leggi di revisione della Costituzione possono essere sottoposte a referendum popolare tra l'altro quando, entro tre mesi dalla loro pubblicazione, ne facciano domanda:`, `Cinque Consigli regionali.`, `I Ministri.`, `Almeno un milione di elettori.` ] , 
