      "3322" : [ `"Dei compiti spettanti alla Corte Costituzionale è possibile distinguere tra: funzioni giurisdizionali e funzioni amministrative; funzioni di garanzia e funzioni arbitrali; attribuzioni di legittimità e attribuzioni di merito. Quando svolge funzioni giurisdizionali:"`, `Alla Corte spetta il compito di risolvere una vera e propria controversia.`, `La Corte opera una mera verifica sull'esistenza o meno di determinati presupposti.`, `La Corte opera come garante dei cittadini contro eventuali violazioni al testo della Costituzione.` ] , 
      "3323" : [ `"Dei compiti spettanti alla Corte Costituzionale è possibile distinguere tra: funzioni giurisdizionali e funzioni amministrative; funzioni di garanzia e funzioni arbitrali; attribuzioni di legittimità e attribuzioni di merito. Quando svolge funzioni di garanzia:"`, `La Corte opera come garante dei cittadini contro eventuali violazioni al testo della Costituzione.`, `Alla Corte spetta il compito di risolvere una vera e propria controversia.`, `La Corte opera una mera verifica sull'esistenza o meno di determinati presupposti.` ] , 
      "3324" : [ `"Dei compiti spettanti alla Corte Costituzionale è possibile distinguere tra: funzioni giurisdizionali e funzioni amministrative; funzioni di garanzia e funzioni arbitrali; attribuzioni di legittimità e attribuzioni di merito. Quando svolge funzioni arbitrali:"`, `La Corte risolve i conflitti che possono insorgere tra le varie cariche dello Stato e delle Regioni.`, `La Corte opera come garante dei cittadini contro eventuali violazioni al testo della Costituzione.`, `La Corte opera una mera verifica sull'esistenza o meno di determinati presupposti.` ] , 
