      "3333" : [ `Quando la Corte Costituzionale giudica sui conflitti di attribuzione tra i poteri dello Stato e su quelli tra lo Stato e le Regioni, e tra le Regioni:`, `Svolge un controllo di legittimità.`, `Svolge una funzione amministrativa.`, `Svolge un controllo di merito.` ] , 
