      "3338" : [ `Chi è competente ad accertare l'esistenza dei requisiti soggettivi di ammissione dei componenti ordinari e di quelli aggregati della Corte Costituzionale?`, `La Corte stessa.`, `Il Presidente della Repubblica.`, `Il Presidente del Consiglio dei Ministri.` ] , 
      "3339" : [ `Chi è competente ad accertare l'esistenza dei requisiti soggettivi di ammissione dei giudici della Corte Costituzionale?`, `La Corte stessa.`, `Il Parlamento.`, `Il Presidente del Senato.` ] , 
      "3340" : [ `"La struttura della Corte Costituzionale è contraddistinta dalla diversa estrazione dei quindici giudici costituzionali. Per quanto attiene ai giudici eletti dalle “supreme magistrature”,:"`, `Tre sono scelti dalla Cassazione, uno dal Consiglio di Stato ed uno dalla Corte dei conti.`, `Due sono scelti dalla Cassazione, tre dal Consiglio di Stato.`, `Dieci sono scelti dalla Cassazione, tre dalla Corte dei conti.` ] , 
      "3341" : [ `Le udienze della Corte Costituzionale sono pubbliche?`, `Si, di norma sono pubbliche.`, `No, si svolgono a porte chiuse.`, `No, si svolgono a porte chiuse, salvo che il Presidente decida di ammettervi il pubblico.` ] , 
      "3342" : [ `Una udienza della Corte Costituzionale può svolgersi a porte chiuse?`, `Si. Le udienze si svolgono a porte chiuse quando la pubblicità può nuocere alla sicurezza dello Stato o all'ordine pubblico o alla morale.`, `Si, ma solo quando, da parte del pubblico, avvengono manifestazioni che possano turbare la serenità.`, `No, in alcun caso.` ] , 
      "3343" : [ `La Corte costituzionale:`, `È organo costituzionale.`, `Non gode di autodichia, infatti la Corte non esercita la giurisdizione sui ricorsi dei propri dipendenti.`, `Gode di autonomia finanziaria ma non di autonomia amministrativa.` ] , 
