      "3345" : [ `La Corte costituzionale:`, `È imparziale, in quanto i sui membri sono svincolati da interessi di parte.`, `Non è organo costituzionale ma organo di rilievo costituzionale.`, `Gode di autonomia organizzativa ma non di autonomia amministrativa.` ] , 
