      "3347" : [ `La Corte costituzionale:`, `È un collegio super partes, in quanto è svincolato da interessi dei partiti e dagli altri organi costituzionali.`, `Gode di autonomia regolamentare ma non di autonomia amministrativa.`, `"Nei giudizi di accusa contro il Presidente del Consiglio dei Ministri è integrata daic.d. giudici non togati."` ] , 
      "3348" : [ `La Corte costituzionale:`, `É un organo superiorem non recognoscens, e come tale costituente un potere dello Stato.`, `Gode di autonomia organizzativa ma non di autonomia amministrativa.`, `Elegge tra i suoi componenti il Presidente che resta in carica cinque anni ed è rieleggibile.` ] , 
      "3349" : [ `La Corte costituzionale:`, `Gode di autodichia, infatti la Corte esercita in via esclusiva la giurisdizione sui ricorsi dei propri dipendenti sulla base di un proprio regolamento.`, `Gode di autonomia organizzativa ma non di autonomia finanziaria.`, `È giudice del merito e non della legittimità costituzionale delle leggi.` ] , 
      "3350" : [ `La Corte costituzionale:`, `É competente ad accertare l'esistenza dei requisiti soggettivi di ammissione dei componenti ordinari e di quelli aggregati della Corte stessa.`, `Gode di autonomia finanziaria ma non di autonomia amministrativa.`, `È giudice del merito e non della legittimità costituzionale delle leggi.` ] , 
      "3351" : [ `La Corte costituzionale:`, `Ha come compito fondamentale quello di giudicare della legittimità costituzionale delle leggi.`, `Non gode di autonomia finanziaria.`, `È organo monocratico e collegiale in quanto esercita le sue funzioni non solo in composizione collegiale.` ] , 
      "3352" : [ `Indicare quale delle seguenti affermazioni relative alla Corte costituzionale non è corretta.`, `Nella sua composizione ordinaria, si compone di 12 giudici.`, `Gode di autonomia amministrativa.`, `É un organo collegiale.` ] , 
      "3353" : [ `Indicare quale delle seguenti affermazioni relative alla Corte costituzionale non è corretta.`, `"Nei giudizi di accusa contro il Presidente del Consiglio dei Ministri è integrata daic.d. giudici non togati."`, `Gode di autonomia organizzativa.`, `É un organo costituzionale.` ] , 
      "3354" : [ `Indicare quale delle seguenti affermazioni relative alla Corte costituzionale non è corretta.`, `Elegge tra i suoi componenti il Presidente che resta in carica cinque anni ed è rieleggibile.`, `Gode di autodichia, infatti la Corte esercita in via esclusiva la giurisdizione sui ricorsi dei propri dipendenti sulla base di un proprio regolamento.`, `Ha come compito fondamentale quello di giudicare della legittimità costituzionale delle leggi.` ] , 
      "3355" : [ `Indicare quale delle seguenti affermazioni relative alla Corte costituzionale non è corretta.`, `Non gode di autonomia regolamentare.`, `Nella sua composizione ordinaria, si compone di 15 giudici.`, `Elegge tra i suoi componenti il Presidente che resta in carica tre anni ed è rieleggibile.` ] , 
      "3356" : [ `Indicare quale delle seguenti affermazioni relative alla Corte costituzionale non è corretta.`, `Non è organo costituzionale ma organo di rilievo costituzionale.`, `Ha come compito fondamentale quello di giudicare della legittimità costituzionale delle leggi.`, `Nella sua composizione ordinaria, si compone di 15 giudici.` ] , 
      "3357" : [ `Come sono adottati i provvedimenti di competenza della Corte Costituzionale?`, `Con ordinanza o con sentenza.`, `Con sentenza ovvero con decreto.`, `Con decreto.` ] , 
      "3358" : [ `La Corte costituzionale può pronunciare sentenze di: cessazione del contendere, inammissibilità, accoglimento, rigetto, interpretative di rigetto. In caso di intervenuta abrogazione o modificazione della legge durante lo svolgimento del processo, pronuncia una sentenza di:`, `Cessazione del contendere.`, `Inammissibilità.`, `Accoglimento.` ] , 
      "3359" : [ `La Corte costituzionale può pronunciare sentenze di: cessazione del contendere, inammissibilità, accoglimento, rigetto, interpretative di rigetto. Quando manchino i presupposti per procedere ad un giudizio di merito (es. l’atto impugnato non rientra fra quelli previsti dall'art. 134 Cost.), pronuncia una sentenza di:`, `Inammissibilità.`, `Cessazione del contendere.`, `Accoglimento.` ] , 
