      "3460" : [ `Dispone la Costituzione all'art. 57 che la ripartizione dei seggi tra le Regioni, fatto salvo il numero dei seggi assegnati alla circoscrizione Estero, si effettua:`, `In proporzione alla popolazione delle Regioni, quale risulta dall'ultimo censimento generale, sulla base dei quozienti interi e dei più alti resti.`, `In proporzione alla popolazione e all'estensione del territorio delle Regioni, sulla base dei quozienti interi e dei più alti resti.`, `Dividendo il numero degli abitanti della Repubblica, quale risulta dall'ultimo censimento generale della popolazione, per trecentoquindici e distribuendo i seggi in proporzione alla popolazione di ogni circoscrizione, sulla base dei quozienti interi e dei più alti resti.` ] , 
      "3461" : [ `Indicare quale affermazione sulla composizione del Parlamento è corretta.`, `I senatori sono eletti a suffragio universale e diretto dai cittadini che hanno superato  il venticinquesimo anno di età.`, `"Nessuna Regione può avere un numero di deputati inferiore a sette; il Molise ne ha due, la Valle d'Aosta uno."`, `La Camera dei deputati si compone di trecentoquindici deputati.` ] , 
      "3462" : [ `Indicare quale affermazione sulla composizione del Parlamento è corretta.`, `Sono elettori della Camera dei deputati tutti i cittadini che hanno raggiunto la maggiore età.`, `La Camera dei deputati si compone di trecentoquindici deputati.`, `Il Senato della Repubblica è eletto a suffragio universale e indiretto.` ] , 
      "3463" : [ `Con riferimento alla composizione delle due Camere è costituzionalmente corretto affermare che:`, `Sono eleggibili a senatori, gli elettori che hanno compiuto il quarantesimo anno di età.`, `La composizione elettiva della Camera dei deputati è integrata dalla presenza degli ex Presidenti della Repubblica.`, `La Camera dei deputati è eletta a base regionale, salvi i seggi assegnati alla circoscrizione Estero.` ] , 
      "3464" : [ `"La Costituzione fissa il numero dei senatori in trecentoquindici; quanti sono quelli eletti nella circoscrizione Estero?"`, `Sei.`, `Dodici.`, `Cinque.` ] , 
      "3465" : [ `Con la l. 1/2001 Cost. è stato modificato l'art. 57 Cost. che ha previsto:`, `L'attribuzione di sei senatori alla circoscrizione Estero del Senato.`, `L'attribuzione di dodici senatori alla circoscrizione Estero del Senato.`, `L'attribuzione di dieci senatori alla circoscrizione Estero del Senato.` ] , 
