      "3469" : [ `La Costituzione italiana fissa in 630 il numero dei Deputati. I dodici deputati eletti nella circoscrizione Estero:`, `Sono compresi tra i 630 membri.`, `Sono da aggiungere ai 630 membri.`, `Non sono compresi tra i 630 come quelli eletti nel Senato.` ] , 
      "3470" : [ `Il Parlamento si riunisce in seduta comune dei membri delle due Camere:`, `Nei soli casi stabiliti dalla Costituzione.`, `Quando lo richiede il Presidente della Repubblica.`, `Nei soli casi stabiliti dalla Costituzione e quando lo richieda il Presidente della Repubblica.` ] , 
