      "3476" : [ `Il Parlamento si riunisce in seduta comune, tra l'altro, per:`, `Mettere in stato di accusa il Presidente della Repubblica.`, `Eleggere le massime cariche dello Stato.`, `Delegare l'esercizio della funzione legislativa.` ] , 
