      "3478" : [ `Dal Parlamento come organo complesso, va distinto il Parlamento in seduta comune dei membri delle due Camere, che non è un terzo ramo del Parlamento, ma organo collegiale a sé stante, che si riunisce nei soli casi previsti dalla Costituzione. Il Parlamento in seduta comune si riunisce, tra l'altro,:`, `Per la compilazione dell'elenco di cittadini aventi i requisiti per l'eleggibilità a senatore, dal quale trarre a sorte i sedici giudici aggregati, chiamati a integrare la composizione della Corte costituzionale nei giudizi di accusa contro il Presidente della Repubblica.`, `Per eleggere i componenti della Giunta delle elezioni e delle immunità parlamentari.`, `Quando svolge funzioni di indirizzo politico.` ] , 
      "3479" : [ `Il bicameralismo perfetto esige che quasi tutte le deliberazioni vengano prese da entrambe le Camere separatamente. Una eccezione è:`, `Per la compilazione dell'elenco di cittadini aventi i requisiti per l'eleggibilità a senatore, dal quale trarre a sorte i sedici giudici aggregati, chiamati a integrare la composizione della Corte costituzionale nei giudizi di accusa contro il Presidente della Repubblica.`, `L'elezione dei componenti della Giunta delle elezioni e delle immunità parlamentari.`, `L'approvazione in via definitiva i provvedimenti legislativi.` ] , 
