      "3482" : [ `"Dal Parlamento come organo complesso va distinto il Parlamento in seduta comune dei membri delle due Camere che è un organo a sé stante e più specificamente un organo collegiale che non esercita funzioni legislative bensì funzioni di diversa natura, tassativamente indicate nella Cost. Una di queste ècostituita:"`, `Dall'elezione di un terzo dei membri del CSM.`, `Dalla determinazione dei casi di incompatibilità con l'ufficio di parlamentare.`, `Dalla proroga dei poteri delle Camere.` ] , 
