      "3524" : [ `Secondo la Costituzione, entro quanti giorni hanno luogo le elezioni delle nuove Camere?`, `Entro settanta giorni dalla fine delle precedenti.`, `Entro sessanta giorni dalla fine delle precedenti.`, `Entro novanta giorni dalla fine delle precedenti.` ] , 
      "3525" : [ `Ai sensi della Costituzione, le elezioni delle nuove Camere hanno luogo entro:`, `Settanta giorni dalla fine delle precedenti.`, `Cinquanta giorni dalla fine delle precedenti.`, `Sessanta giorni dalla fine delle precedenti.` ] , 
