      "3531" : [ `È corretto affermare che la prima riunione delle nuove Camere deve aver luogo entro quaranta giorni dalla data delle elezioni?`, `No, deve aver luogo entro venti giorni.`, `Si, per espressa previsione di cui all’art. 61 Cost.`, `Nessuna delle altre risposte è corretta, la Costituzione nulla dispone in merito alla prima riunione delle Camere.` ] , 
      "3532" : [ `Nell'intervallo di tempo tra la fine della legislatura e la riunione delle nuove Camere, il potere legislativo è esercitato:`, `Dalle precedenti Camere, alle quali sono prorogati i poteri.`, `Dai Presidenti delle due Camere.`, `Dal Governo, che può sempre emanare i decreti-legge.` ] , 
      "3533" : [ `Il potere legislativo, nell'intervallo di tempo tra la fine della legislatura e la riunione delle nuove Camere,:`, `É esercitato dalle precedenti Camere, alle quali sono prorogati i poteri.`, `É esercitato dal Governo, che può sempre emanare i decreti-legge e i decreti legislativi.`, `Non è esercitato da alcun organo. L'attività legislativa è sospesa.` ] , 
      "3534" : [ `Le elezioni delle nuove Camere hanno luogo entro settanta giorni dalla fine delle precedenti. Finché non siano riunite le nuove Camere il potere legislativo:`, `É esercitato dalle precedenti Camere, alle quali sono prorogati i poteri.`, `É esercitato dal Presidente del Senato.`, `É esercitato dal Consiglio dei Ministri.` ] , 
      "3535" : [ `Con riferimento alla durata delle Camere i termini proroga e prorogatio esprimono lo stesso concetto?`, `No, il secondo è riferito alla continuità delle funzioni attraverso la proroga dei poteri delle Camere scadute o anticipatamente sciolte finché non sono riunite le nuove Camere.`, `Si, esprimono lo spesso concetto.`, `No, il primo si verifica solo nel caso di decesso del Presidente della Repubblica, il secondo si verifica solo nel caso di decesso di uno dei due Presidenti delle Camere.` ] , 
      "3536" : [ `La Costituzione prevede due ipotesi di proroga dei poteri delle Camere, una denominata proroga di durata e una prorogatio. La proroga di durata:`, `È quella di cui all’art. 60 della Costituzione disposta con legge e solo in caso di guerra.`, `È quella prevista all’art. 61 della Costituzione che consente alle Camere di continuare a esercitare i poteri fino alla riunione del nuovo Parlamento.`, `È quella prevista all’art. 83 della Costituzione al fine di consentire l’elezione del nuovo Presidente della Repubblica alla scadenza del mandato.` ] , 
      "3537" : [ `La Costituzione prevede due ipotesi di proroga dei poteri delle Camere, una denominata proroga di durata e una prorogatio. La prorogatio:`, `È quella prevista all’art. 61 della Costituzione che consente alle Camere di continuare a esercitare i poteri fino alla riunione del nuovo Parlamento.`, `È quella di cui all’art. 60 della Costituzione disposta con legge e solo in caso di guerra.`, `È quella prevista all’art. 83 della Costituzione al fine di consentire l’elezione del nuovo Presidente della Repubblica alla scadenza del mandato.` ] , 
      "3538" : [ `"Con riferimento alla convocazione delle Camere esistono casi in cui esse si riuniscono “di diritto” per espressa previsione della Cost.?"`, `Si, uno dei casi è la riunione di diritto il primo giorno non festivo di febbraio e di ottobre prevista dal comma 1, art. 62, Cost.`, `No, la Cost. prevede solo la convocazione ordinaria, la convocazione straordinaria e la convocazione su mozione di aggiornamento.`, `No, la Cost. prevede solo la convocazione ordinaria e la convocazione straordinaria.` ] , 
      "3539" : [ `Esistono giorni in cui le Camere si riuniscono di diritto per espressa previsione costituzionale?`, `Si, le Camere si riuniscono di diritto il primo giorno non festivo di febbraio e di ottobre.`, `Si, le Camere si riuniscono di diritto il primo giorno non festivo di ogni mese.`, `No. La riunione di una Camera presuppone la convocazione del suo Presidente o di un terzo dei componenti l'assemblea o del Capo dello Stato.` ] , 
