      "3668" : [ `Per i disegni di legge in materia di delegazione legislativa:`, `É sempre adottata la procedura normale di esame e di approvazione diretta da parte della Camera.`, `Può essere adottata la c.d. procedura mista.`, `Può essere adottata la procedura c.d. abbreviata.` ] , 
      "3669" : [ `Per i disegni di legge di autorizzazione a ratificare trattati internazionali:`, `É sempre adottata la procedura normale di esame e di approvazione diretta da parte della Camera.`, `É sempre adottato il procedimento decentrato (o in Commissione).`, `Può essere adottata la c.d. procedura mista.` ] , 
      "3670" : [ `Per i disegni di legge di approvazione del rendiconto consuntivo dello Stato:`, `É sempre adottata la procedura normale di esame e di approvazione diretta da parte della Camera.`, `É sempre adottato il procedimento decentrato (o in Commissione).`, `Può essere adottata la procedura c.d. abbreviata.` ] , 
      "3671" : [ `Per i disegni di legge di approvazione del bilancio dello Stato:`, `É sempre adottata la procedura normale di esame e di approvazione diretta da parte della Camera.`, `"Può essere adottata la c.d. procedura mista."`, `Può essere adottata la procedura c.d. abbreviata.` ] , 
      "3672" : [ `Per i disegni di legge di approvazione di bilanci e consuntivi:`, `É sempre adottata la procedura normale di esame e di approvazione diretta da parte della Camera.`, `É sempre adottato il procedimento decentrato (o in Commissione).`, `Può essere adottata la procedura c.d. abbreviata.` ] , 
      "3673" : [ `La procedura normale di esame e di approvazione diretta da parte della Camera è sempre adottata (art. 72, Cost.):`, `Per i disegni di legge in materia elettorale.`, `Per i disegni di legge in materia di testi unici.`, `Per i disegni di legge in materia di attività professionali.` ] , 
