      "3726" : [ `Quali tra i seguenti sono istituti (c.d. attività parlamentari di garanzia) a tutela prevalentemente delle minoranze che consentono il controllo dell'operato del Governo?`, `L'interrogazione e l'interpellanza.`, `La mozione e la risoluzione.`, `La mozione e l'interpellanza.` ] , 
