      "3728" : [ `Con riferimento agli istituti con i quali le Camere svolgono la loro attività conoscitiva e ispettiva (es. mozione, risoluzione, interrogazione, interpellanza, inchieste), quale istituto ha il fine di promuovere una deliberazione dell'Assemblea su un determinato argomento?`, `Mozione.`, `Inchiesta.`, `Interrogazione.` ] , 
