      "3730" : [ `Con riferimento agli istituti con i quali le Camere svolgono la loro attività conoscitiva e ispettiva (es. mozione, risoluzione, interrogazione, interpellanza, inchieste), quale istituto è diretto a manifestare orientamento o a definire indirizzi su specifici argomenti?`, `Risoluzione.`, `Mozione.`, `Inchiesta.` ] , 
      "3731" : [ `Con riferimento agli istituti con i quali le Camere svolgono la loro attività conoscitiva e ispettiva (es. mozione, risoluzione, interrogazione, interpellanza, inchieste), quale istituto consiste nella semplice domanda, rivolta da un parlamentare per iscritto al Governo, o ad un singolo ministro, per conoscere se un fatto sia vero, se alcuna informazione sia giunta al Governo, o sia esatta, se il Governo intenda comunicare alla Camera documenti o notizie o abbia preso o stia prendendo alcun provvedimento su un soggetto determinato?`, `Interrogazione.`, `Mozione.`, `Risoluzione.` ] , 
      "3732" : [ `Con riferimento agli istituti con i quali le Camere svolgono la loro attività conoscitiva e ispettiva (es. mozione, risoluzione, interrogazione, interpellanza, inchieste), quale istituto consiste nella domanda rivolta per iscritto da un parlamentare al Governo o ad un ministro per verificare la conoscenza di una determinata situazione?`, `Interrogazione.`, `Interpellanza.`, `Mozione.` ] , 
      "3733" : [ `Con riferimento agli istituti con i quali le Camere svolgono la loro attività conoscitiva e ispettiva (es. mozione, risoluzione, interrogazione, interpellanza, inchieste), quale istituto consiste nella domanda, rivolta per iscritto, al Governo (o ad un singolo ministro) circa i motivi o gli intendimenti della condotta del Governo, in questioni che riguardano determinati aspetti della sua politica?`, `Interpellanza.`, `Interrogazione.`, `Inchiesta.` ] , 
      "3734" : [ `Con riferimento agli istituti con i quali le Camere svolgono la loro attività conoscitiva e ispettiva (es. mozione, risoluzione, interrogazione, interpellanza, inchieste), quale istituto consiste nella domanda rivolta per iscritto da un parlamentare al Governo o ad un ministro circa i motivi o gli intendimenti della condotta politica tenuta rispetto ad una data questione?`, `Interpellanza.`, `Interrogazione.`, `Risoluzione.` ] , 
      "3735" : [ `"Con riferimento agli istituti con i quali le Camere svolgono la loro attività conoscitiva e ispettiva (es. mozione, risoluzione, interrogazione, interpellanza, inchieste), quale istituto è diretto ad accertare la situazione dell'ordine pubblico in una determinata parte del Paese, le responsabilità di funzionari e di uomini politici in relazione ad una certa situazione di pubblicointeresse?"`, `Inchiesta.`, `Mozione.`, `Risoluzione.` ] , 
      "3736" : [ `Il Regolamento del Senato della Repubblica:`, `É adottato dallo stesso Senato.`, `É approvato dal Parlamento in seduta comune.`, `É approvato dalla Commissione affari costituzionali.` ] , 
