      "3740" : [ `"A norma del disposto di cui all'art. 64 della Costituzione chi adotta il Regolamento della Camere dei deputati?Quale maggioranza e richiesta?"`, `La stessa Camera a maggioranza assoluta dei suoi componenti.`, `Il presidente del Consiglio a maggioranza assoluta dei suoi componenti del governo.`, `La stessa Camera a maggioranza dei due terzi dei suoi componenti.` ] , 
