      "3742" : [ `I Regolamenti parlamentari di cui all'art. 64 della Costituzione:`, `Sono adottati a maggioranza assoluta dei suoi componenti.`, `Sono adottati dalla Presidenza della Repubblica.`, `Disciplina anche l'organizzazione interna anche della Presidenza del Consiglio dei Ministri.` ] , 
