      "3747" : [ `Il Regolamento del Senato previsto all'art. 64 della Costituzione:`, `Detta le regole per il funzionamento del Senato.`, `È adottato dalla Presidenza della Repubblica.`, `È adottato a maggioranza semplice dei suoi componenti.` ] , 
      "3748" : [ `Il Regolamento della Camera dei Deputati previsto all'art. 64 della Costituzione:`, `È adottato dalla stessa Camera.`, `È adottato a maggioranza dei due terzi dei suoi componenti.`, `Disciplina l'organizzazione interna anche delle Senato.` ] , 
      "3749" : [ `Il Regolamento della Camera dei Deputati previsto all'art. 64 della Costituzione:`, `È adottato a maggioranza assoluta dei suoi componenti.`, `È adottato dalla Presidenza della Repubblica.`, `Detta le regole per il funzionamento anche per il Senato.` ] , 
      "3750" : [ `Il Regolamento della Camera dei Deputati previsto all'art. 64 della Costituzione:`, `Disciplina l'organizzazione interna della Camera dei deputati.`, `È adottato dalla Presidenza del Consiglio dei Ministri.`, `È adottato a maggioranza semplice dei suoi componenti.` ] , 
