      "3754" : [ `La Camera dei deputati gode di autonomia regolamentare?`, `Si, per espressa previsione di cui all'art. 64 della Costituzione.`, `No, la Camera dei deputati gode solo di autonomia finanziaria e amministrativa.`, `Si, la Camera dei deputati gode solo di autonomia regolamentare.` ] , 
