      "3760" : [ `Indicare quale affermazione afferente l'autonomia finanziaria delle Camere è corretta.`, `Ciascuna camera delibera il proprio bilancio preventivo.`, `Le spese delle Camere gravano su un fondo speciale, che è somministrato e gestito dal Ministero dell'economia e delle finanze.`, `L'autonomia finanziaria non esclude il controllo esterno della Corte dei Conti.` ] , 
