      "3773" : [ `"Con riferimento alle garanzie delle Camere, in che cosa consiste la c.d. “immunità della sede”?"`, `Nel divieto posto alla forza pubblica, di accedere agli edifici delle Camere.`, `Nel divieto di eseguire intercettazioni ambientali e telefoniche nei confronti dei membri delle due Camere.`, `Nell'impossibilità per il pubblico di accedere alle sedi delle Camere, ovvero ai locali degli organi e uffici delle Camere, senza autorizzazione di uno dei due Presidenti.` ] , 
