      "3879" : [ `È corretto affermare che il compito di promuovere e coordinare l'attività dei Ministri spetta al Presidente della Repubblica?`, `No, spetta al Presidente del Consiglio dei Ministri.`, `No, spetta al Parlamento.`, `Si, spetta al Presidente della Repubblica.` ] , 
      "3880" : [ `È corretto affermare che il compito di promuovere e coordinare l'attività dei Ministri spetta al Presidente del Consiglio dei Ministri?`, `Si, spetta al Presidente del Consiglio dei Ministri.`, `No, spetta al Parlamento.`, `No, spetta al Presidente della Repubblica.` ] , 
      "3881" : [ `Secondo la Costituzione, i Ministri sono responsabili degli atti del Consiglio dei Ministri?`, `Si, collegialmente.`, `Si, individualmente.`, `No, i Ministri non sono mai responsabili per gli atti compiuti nell'esercizio delle loro funzioni.` ] , 
      "3882" : [ `Secondo la Costituzione, i Ministri sono responsabili dell'attività dei loro dicasteri?`, `Si, individualmente.`, `No.`, `Si, collegialmente.` ] , 
