      "3884" : [ `A norma del disposto di cui all'art. 95 della Costituzione, i Ministri per gli atti esclusivamente di loro competenza:`, `Sono responsabili individualmente.`, `Sono responsabili collegialmente.`, `Non sono responsabili.` ] , 
      "3885" : [ `A norma del disposto di cui all'art. 95 della Costituzione, i Ministri per gli atti del Consiglio dei Ministri:`, `Sono responsabili collegialmente.`, `Sono responsabili individualmente.`, `Non sono responsabili.` ] , 
      "3886" : [ `In materia di responsabilità dei Ministri, la Costituzione statuisce che i Ministri sono:`, `Responsabili collegialmente degli atti del Consiglio.`, `Sempre irresponsabili degli atti compiuti nell'esercizio della loro funzione.`, `Responsabili solo degli atti dei loro dicasteri.` ] , 
      "3887" : [ `In materia di responsabilità dei Ministri, la Costituzione statuisce che i Ministri sono:`, `Responsabili individualmente degli atti dei loro dicasteri.`, `Sempre irresponsabili degli atti compiuti nell'esercizio della loro funzione.`, `Responsabili solo degli atti dei loro dicasteri.` ] , 
