      "3889" : [ `A norma del disposto di cui all'art. 95 della Costituzione è corretto affermare che i Ministri sono responsabili solo degli atti del Consiglio di Ministri?`, `No, i Ministri sono responsabili sia degli atti dei loro dicasteri sia degli atti del Consiglio dei Ministri.`, `No, i Ministri sono responsabili solo degli atti dei loro dicasteri.`, `Si, i Ministri sono responsabili solo degli atti del Consiglio dei Ministri.` ] , 
      "3890" : [ `A norma del disposto di cui all'art. 95 della Costituzione è corretto affermare che i Ministri sono responsabili sia degli atti del loro dicastero sia degli atti del Consiglio dei Ministri?`, `Si, per gli atti dei loro dicasteri sono responsabili individualmente, per gli atti del Consiglio dei Ministri sono responsabili collegialmente.`, `"Si; sono responsabili sempre individualmente."`, `No, sono responsabili solo degli atti dei loro dicasteri.` ] , 
