      "4165" : [ `Il Consiglio di Sicurezza dell'ONU è composto da 15 membri, 5 di essi sono membri permanenti, gli altri 10 hanno mandato biennale. Quale tra i seguenti è un membro permanente?`, `Cina.`, `Spagna.`, `Canada.` ] , 
