      "4368" : [ `Indicare quale affermazione sulla fase di integrazione dell'efficacia della legge è correttamente riferibile a quanto dispone l'art. 73 della Costituzione.`, `Se le Camere, ciascuna a maggioranza assoluta dei propri componenti, ne dichiarano l'urgenza, la legge è promulgata nel termine da essa stabilito.`, `Le leggi sono promulgate entro quindici giorni dall'approvazione.`, `Le leggi sono promulgate subito dopo la pubblicazione.` ] , 
      "4369" : [ `Indicare quale affermazione sulla fase di integrazione dell'efficacia della legge è correttamente riferibile a quanto dispone l'art. 73 della Costituzione.`, `Le leggi sono pubblicate subito dopo la promulgazione.`, `Se il Presidente della Repubblica, ne dichiara l'urgenza, la legge è promulgata nel termine da essa stabilito.`, `Le leggi entrano in vigore, di norma, il trentesimo giorno successivo alla loro pubblicazione.` ] , 
      "4370" : [ `Indicare quale affermazione sulla fase di integrazione dell'efficacia della legge è correttamente riferibile a quanto dispone l'art. 73 della Costituzione.`, `Le leggi entrano in vigore, di norma, il quindicesimo giorno successivo alla loro pubblicazione.`, `Se il Presidente della Repubblica, ne dichiara l'urgenza, la legge è promulgata nel termine da essa stabilito.`, `Le leggi sono promulgate entro quindici giorni dall'approvazione.` ] , 
