      "4373" : [ `Indicare quale affermazione sulla fase di integrazione dell'efficacia della legge non è correttamente riferibile a quanto dispone l'art. 73 della Costituzione.`, `Le leggi non necessitano di pubblicazione.`, `Se le Camere, ciascuna a maggioranza assoluta dei propri componenti, ne dichiarano l'urgenza, la legge è promulgata nel termine da essa stabilito.`, `Le leggi sono pubblicate subito dopo la promulgazione.` ] , 
      "4374" : [ `Indicare quale affermazione sulla fase di integrazione dell'efficacia della legge non è correttamente riferibile a quanto dispone l'art. 73 della Costituzione.`, `Le leggi entrano in vigore, di norma, il decimo giorno successivo alla loro pubblicazione.`, `Le leggi sono promulgate entro un mese dall'approvazione.`, `Le leggi sono pubblicate subito dopo la promulgazione.` ] , 
      "4375" : [ `Indicare quale affermazione sulla fase di integrazione dell'efficacia della legge non è correttamente riferibile a quanto dispone l'art. 73 della Costituzione.`, `Se le Camere, ciascuna a maggioranza dei due terzi dei propri componenti, ne dichiarano l'urgenza, la legge è promulgata nel termine da essa stabilito.`, `Le leggi sono promulgate entro un mese dall'approvazione.`, `Le leggi sono pubblicate subito dopo la promulgazione.` ] , 
      "4376" : [ `Il Parlamento può delegare al Governo l'esercizio della funzione legislativa?`, `No, se non con determinazione di principi e criteri direttivi e soltanto per tempo limitato e per oggetti definiti.`, `Si, tale esercizio è limitato ai casi di necessità ed urgenza.`, `Si. Tutta l'attività legislativa può essere delegata al Governo.` ] , 
