      "4378" : [ `Dispone la Costituzione che il Governo non può, senza delegazione delle Camere, emanare decreti che abbiano valore di legge ordinaria. A quali fonti normative si riferisce il precetto costituzionale?`, `Ai decreti legislativi.`, `Ai decreti-legge.`, `Ai regolamenti governativi.` ] , 
