      "4401" : [ `L'art. 76 della Costituzione consente al Parlamento di delegare l'esercizio della funzione legislativa al Governo. Tale delega:`, `Non può avere ad oggetto l'autorizzazione alla ratifica dei trattati internazionali.`, `Può non indicare un oggetto definito.`, `Può avere ad oggetto l'approvazione del bilancio.` ] , 
