      "4498" : [ `La ratifica dei trattati internazionali che importano variazioni del territorio:`, `É autorizzata dalle Camere.`, `É autorizzata dal Ministro dell'economia e delle finanze.`, `É autorizzata dal Presidente del Consiglio dei Ministri.` ] , 
