      "4500" : [ `É corretto affermare che le Camere, autorizzano con legge, la ratifica di trattati internazionali che importano variazioni del territorio?`, `Si, tale autorizzazione rientra nelle competenze delle Camere previste dall'art. 80 della Costituzione.`, `No, le autorizzazioni previste dall'art. 80 della Costituzione riguardano i soli trattati internazionali che importano modificazioni di leggi.`, `No, le autorizzazioni previste dall'art. 80 della Costituzione riguardano i soli trattati internazionali che importano oneri alle finanze.` ] , 
      "4501" : [ `É corretto affermare che le Camere, autorizzano con legge, la ratifica di trattati internazionali che sono di natura politica?`, `Si, lo prevede espressamente l'art. 80 della Costituzione.`, `No, le autorizzazioni previste dall'art. 80 della Costituzione riguardano i soli trattati internazionali che prevedono oneri alle finanze.`, `Si, l'art. 80 della Costituzione prevede infatti, l'autorizzazione delle Camere per la ratifica dei soli trattati internazionali che sono di natura politica.` ] , 
      "4502" : [ `É corretto affermare che le Camere, autorizzano con legge, la ratifica di trattati internazionali che sono di natura politica?`, `Si, tale autorizzazione è espressamente prevista dall'art. 80 della Costituzione.`, `No, tale autorizzazione è competenza propria del Presidente del Consiglio dei Ministri.`, `No, le autorizzazioni previste dall'art. 80 della Costituzione riguardano i soli trattati internazionali che prevedono arbitrati.` ] , 
      "4503" : [ `A quale organo compete, ai sensi dell'art. 80 della Costituzione, l'autorizzazione alla ratifica di trattati internazionali di natura politica?`, `Camere, con legge.`, `Presidente del Consiglio e Ministro degli Affari Esteri, congiuntamente.`, `Consiglio dei Ministri.` ] , 
      "4504" : [ `A norma di quanto dispone l'art. 80 della Costituzione, la ratifica dei trattati internazionali che importano modificazioni di leggi:`, `É autorizzata dalle Camere.`, `É autorizzata dal Ministro dell'economia e delle finanze.`, `É autorizzata dal Ministro per le politiche comunitarie.` ] , 
      "4505" : [ `A quale organo compete, ai sensi dell'art. 80 della Costituzione, l'autorizzazione alla ratifica di trattati internazionali che importano modificazioni di leggi?`, `Camere, con legge.`, `Presidente del Consiglio e Ministro degli Affari Esteri, congiuntamente.`, `Consiglio dei Ministri.` ] , 
      "4506" : [ `La ratifica dei trattati internazionali che importano modificazioni di leggi:`, `É autorizzata dalle Camere.`, `É competenza propria del Presidente del Consiglio dei Ministri.`, `É autorizzata dal Ministro degli Affari Esteri.` ] , 
      "4507" : [ `La ratifica dei trattati internazionali che importano oneri alle finanze:`, `É autorizzata dalle Camere.`, `É competenza propria del Presidente del Consiglio dei Ministri.`, `É autorizzata dal Ministro degli Affari Esteri.` ] , 
      "4508" : [ `A quale organo compete, ai sensi dell'art. 80 della Costituzione, l'autorizzazione alla ratifica di trattati internazionali che importano oneri alle finanze?`, `Camere, con legge.`, `Ministro dell'economia e delle finanze.`, `Corte dei Conti.` ] , 
      "4509" : [ `A norma del dettato Costituzionale, a quale organo compete l'autorizzazione alla ratifica di trattati internazionali che importano oneri alle finanze?`, `Camere, con legge.`, `Presidente del Consiglio dei Ministri.`, `Nessuno, la ratifica di detti trattati non richiede autorizzazione.` ] , 
      "4510" : [ `La ratifica dei trattati internazionali che prevedono arbitrati:`, `É autorizzata dalle Camere.`, `É competenza propria del Presidente della Corte costituzionale.`, `É competenza propria del Presidente del Consiglio dei Ministri.` ] , 
