      "4512" : [ `Chi autorizza la ratifica di trattati internazionali che prevedono regolamenti giudiziari?`, `Le Camere, con legge.`, `Il Presidente della Repubblica.`, `Nessuno, la ratifica di detti trattati non richiede autorizzazione.` ] , 
