      "4517" : [ `A norma del disposto di cui all'art. 81 della Costituzione il ricorso all'indebitamento:`, `È autorizzato dalle Camere.`, `È autorizzato dal Presidente della Repubblica.`, `È autorizzato dal Consiglio dei Ministri.` ] , 
      "4518" : [ `Dispone l'art. 81 della Costituzione che il ricorso all'indebitamento è consentito solo al fine di considerare gli effetti del ciclo economico e, previa autorizzazione delle Camere, al verificarsi di eventi eccezionali, adottata:`, `A maggioranza assoluta dei rispettivi componenti.`, `A maggioranza dei due terzi dei rispettivi componenti.`, `A maggioranza di un terzo dei rispettivi componenti.` ] , 
