      "4611" : [ `Afferma solennemente l'art. 1 della Costituzione italiana che:`, `L'Italia è una Repubblica democratica, fondata sul lavoro.`, `L'Italia è una Repubblica democratica, fondata sulle sue istituzioni.`, `L'Italia è una Repubblica democratica, fondata sulla giustizia, la libertà e il lavoro.` ] , 
      "4612" : [ `Afferma solennemente l'art. 1 della Costituzione italiana che:`, `La sovranità appartiene al popolo, che la esercita nelle forme e nei limiti della Costituzione.`, `L'Italia è una Repubblica democratica, fondata sulle sue istituzioni.`, `La sovranità appartiene al Parlamento e alle autonomie locali.` ] , 
      "4613" : [ `Ai sensi dell'art. 1, comma 2, della Costituzione, la sovranità appartiene:`, `Al popolo.`, `Allo Stato.`, `Alla Repubblica.` ] , 
