      "4728" : [ `La libertà di riunione è garantita ai cittadini dall'art. 17 della Costituzione. Si usa il termine assembramenti:`, `Per indicare, di norma, riunioni occasionali causate da una circostanza improvvisa ed imprevista.`, `Per indicare, di norma, riunioni in movimento dove l'identità del luogo è puramente relativa a soggetti riuniti e compresenti nello stesso spazio, sia che rimangono fermi, sia che si muovono.`, `Per indicare tutte le riunioni che si svolgono senza la preventiva autorizzazione del questore, quindi non autorizzate.` ] , 
