      "4895" : [ `Tra i diritti c.d. sociali contenuti nel titolo II della parte I della Costituzione (rapporti etico - sociali) non rientra:`, `Il diritto di professare liberamente la propria fede religiosa.`, `Il diritto - dovere dei genitori di occuparsi dei figli.`, `Il diritto all'assistenza sanitaria.` ] , 
      "4896" : [ `Tra i diritti c.d. sociali contenuti nel titolo II della parte I della Costituzione (rapporti etico - sociali) non rientra:`, `Il diritto alla libertà di circolazione e soggiorno.`, `Il diritto allo studio.`, `Il diritto all'assistenza sanitaria.` ] , 
      "4897" : [ `Quale principio è contenuto all'art. 35 della Costituzione?`, `La Repubblica tutela il diritto al lavoro in tutte le sue forme ed applicazioni.`, `La Repubblica tutela il diritto al lavoro nei soli casi previsti dalla legge ordinaria.`, `La Repubblica non tutela il diritto al lavoro.` ] , 
      "4898" : [ `A norma della Costituzione, la retribuzione percepita dal lavoratore deve essere:`, `Sufficiente ad assicurare a sé e alla sua famiglia un'esistenza libera e dignitosa.`, `Proporzionata alle esigenze della famiglia media, e comunque non inferiore ai sussidi di disoccupazione.`, `Non inferiore al minimo individuato ogni anno con decreto del Ministro del Lavoro.` ] , 
      "4899" : [ `A norma della Costituzione, quale fonte stabilisce la durata massima della giornata lavorativa?`, `La legge.`, `Il regolamento annuale del Ministero del Lavoro.`, `I regolamenti della Presidenza del Consiglio dei ministri.` ] , 
      "4900" : [ `A norma della Costituzione, il lavoratore può rinunziare al riposo settimanale?`, `No, mai.`, `Si, se lo prevede il contratto di lavoro.`, `Si, se monetizzato.` ] , 
      "4901" : [ `Secondo la Costituzione, può il lavoratore rinunziare alle ferie annuali retribuite?`, `No, mai.`, `No, salvo che la rinunzia sia espressamente pattuita.`, `Si, a condizione che la rinunzia sia espressamente manifestata nel contratto di lavoro subordinato.` ] , 
      "4902" : [ `La donna lavoratrice, a norma della Costituzione:`, `Ha gli stessi diritti dell'uomo e, a parità di lavoro, le stesse retribuzioni.`, `Non ha gli stessi diritti dell'uomo ma, a parità di lavoro, ha le stesse retribuzioni.`, `Ha gli stessi diritti dell'uomo, ma non la stessa retribuzione anche in caso di parità di lavoro.` ] , 
