      "4904" : [ `Secondo la Costituzione, il lavoro dei minori:`, `È tutelato con speciali norme.`, `È vietato senza alcuna eccezione.`, `È limitato a determinate forme di attività.` ] , 
      "4905" : [ `A norma della Costituzione, gli inabili al lavoro hanno diritto al mantenimento e all'assistenza sociale?`, `Si, se sprovvisti dei mezzi necessari per vivere.`, `Si, senza eccezioni.`, `Hanno diritto alla sola assistenza sociale.` ] , 
      "4906" : [ `Dispone l'art. 38 della Costituzione che i lavoratori hanno diritto che siano previsti ed assicurati mezzi adeguati alle loro esigenze di vita. Ciò:`, `In caso di disoccupazione involontaria.`, `In ogni caso.`, `Solo in caso di malattia.` ] , 
      "4907" : [ `Dispone l'art. 38 della Costituzione che i lavoratori hanno diritto che siano previsti ed assicurati mezzi adeguati alle loro esigenze di vita. Ciò:`, `In caso di infortunio.`, `In ogni caso.`, `Solo in caso di vecchiaia.` ] , 
      "4908" : [ `Dispone l'art. 38 della Costituzione che i lavoratori hanno diritto che siano previsti ed assicurati mezzi adeguati alle loro esigenze di vita. Ciò:`, `In caso di invalidità e vecchiaia.`, `In ogni caso.`, `Solo in caso di disoccupazione involontaria.` ] , 
      "4909" : [ `Dispone l'art. 38 della Costituzione che i lavoratori hanno diritto che siano previsti ed assicurati mezzi adeguati alle loro esigenze di vita. Ciò:`, `In caso di malattia.`, `In ogni caso.`, `Solo in caso di vecchiaia.` ] , 
      "4910" : [ `A norma della Costituzione, l'organizzazione sindacale è:`, `Libera.`, `Sottoposta al controllo degli organi competenti per legge.`, `Soggetta ad autorizzazione governativa per l'esercizio dei suoi poteri.` ] , 
      "4911" : [ `La Costituzione della Repubblica italiana riconosce ai lavoratori il diritto di sciopero?`, `Si, all'art. 40.`, `No, soltanto il Codice Civile riconosce il diritto di sciopero.`, `Si, all'art. 2.` ] , 
