      "4920" : [ `Quale delle seguenti affermazioni non è conforme al quadro dei principi costituzionali contenuti nel titolo III della prima parte della Costituzione?`, `Rinunciabilità alle ferie annuali, anche senza compenso.`, `Determinazione della durata massima della giornata lavorativa stabilita dalla legge.`, `Il diritto ad una retribuzione proporzionata alla quantità e qualità del lavoro.` ] , 
      "4921" : [ `Quale delle seguenti affermazioni non è conforme al quadro dei principi costituzionali contenuti nel titolo III della prima parte della Costituzione?`, `Rinunciabilità al riposo settimanale, anche senza compenso.`, `Il diritto ad una retribuzione proporzionata alla quantità e qualità del lavoro.`, `Determinazione dell'età minima per poter svolgere lavoro salariato stabilita dalla legge.` ] , 
      "4922" : [ `Quale delle seguenti affermazioni non è conforme al quadro dei principi costituzionali contenuti nel titolo III della prima parte della Costituzione?`, `La durata massima della giornata lavorativa è stabilita dai C.C.N.L.`, `Il riposo settimanale è irrinunciabile.`, `L'uguaglianza dei diritti tra lavoratore e lavoratrici.` ] , 
      "4923" : [ `Quale delle seguenti affermazioni non è conforme al quadro dei principi costituzionali contenuti nel titolo III della prima parte della Costituzione?`, `L'età minima per poter svolgere lavoro salariato è stabilita dai C.C.N.L.`, `Le ferie annuali retribuite sono irrinunciabili.`, `Il diritto ad una retribuzione proporzionata alla quantità e qualità del lavoro.` ] , 
      "4924" : [ `Quale delle seguenti affermazioni non è conforme al quadro dei principi costituzionali contenuti nel titolo III della prima parte della Costituzione?`, `Assoluto divieto di lavoro per i minori.`, `Parità di retribuzione tra uomini e donne a parità di lavoro.`, `Il diritto ad una retribuzione proporzionata alla quantità e qualità del lavoro.` ] , 
      "4925" : [ `Quale delle seguenti affermazioni non è conforme al quadro dei principi costituzionali contenuti nel titolo III della prima parte della Costituzione?`, `Comprimibilità dei diritti e delle retribuzioni in caso di maternità al fine di salvaguardare i livelli di produzione.`, `Irrinunciabilità del lavoratore alle ferie annuali retribuite.`, `Il diritto all'educazione ed all'avviamento professionale per coloro che sono inabili al lavoro.` ] , 
      "4926" : [ `Quale delle seguenti affermazioni non è conforme al quadro dei principi costituzionali contenuti nel titolo III della prima parte della Costituzione?`, `Determinazione della retribuzione per il lavoro dei minori stabilita dai C.C.N.L.`, `Parità di retribuzione tra uomini e donne a parità di lavoro.`, `Determinazione della durata massima della giornata lavorativa stabilita per legge.` ] , 
      "4927" : [ `Quale delle seguenti affermazioni non è conforme al quadro dei principi costituzionali contenuti nel titolo III della prima parte della Costituzione?`, `Il diritto all'educazione ed all'avviamento professionale per le sole persone abili al lavoro.`, `Parità di retribuzione tra uomini e donne a parità di lavoro.`, `Il diritto ad una retribuzione proporzionata alla quantità e qualità del lavoro.` ] , 
      "4928" : [ `Quale delle seguenti affermazioni non è conforme al quadro dei principi costituzionali contenuti nel titolo III della prima parte della Costituzione?`, `L'assistenza privata non è libera.`, `La repubblica tutela il lavoro italiano all'estero.`, `La durata massima della giornata lavorativa è stabilita dalla legge.` ] , 
      "4929" : [ `Quale delle seguenti affermazioni non è conforme al quadro dei principi costituzionali contenuti nel titolo III della prima parte della Costituzione?`, `Comprimibilità dei diritti per gli inabili ed i minorati.`, `Parità di retribuzione tra uomini e donne a parità di lavoro.`, `Determinazione della durata massima della giornata lavorativa stabilita per legge.` ] , 
      "4930" : [ `Quale delle seguenti affermazioni non è conforme ai principi costituzionali riguardanti la contrattazione collettiva contenuti nel titolo III della prima parte della Costituzione?`, `I sindacati non possono stipulare contratti collettivi di lavoro.`, `Il diritto di sciopero si esercita nell'ambito delle leggi che lo regolano.`, `L'organizzazione sindacale è libera.` ] , 
      "4931" : [ `Quale delle seguenti affermazioni non è conforme ai principi costituzionali riguardanti la contrattazione collettiva contenuti nel titolo III della prima parte della Costituzione?`, `L'organizzazione sindacale non è libera.`, `Il diritto di sciopero si esercita nell'ambito delle leggi che lo regolano.`, `Ai sindacati non può essere imposto altro obbligo se non la loro registrazione presso uffici locali o centrali.` ] , 
      "4932" : [ `Dispone la Costituzione che sono elettori tutti i cittadini, uomini e donne, che hanno raggiunto la maggiore età. Il voto è:`, `Personale ed eguale, libero e segreto.`, `Collettivo ed eguale, libero e segreto.`, `Personale ed eguale, libero e pubblico.` ] , 
