      "4934" : [ `A norma della Costituzione, come è definito l'esercizio del diritto di voto?`, `Un dovere civico.`, `Una semplice facoltà.`, `Una semplice prerogativa.` ] , 
      "4935" : [ `Come definisce il voto la Costituzione italiana?`, `Un dovere civico.`, `Un diritto inalienabile.`, `Un dovere irrinunciabile.` ] , 
