      "4938" : [ `A norma della Costituzione, in quale dei seguenti casi, tra gli altri, il diritto di voto può essere limitato?`, `Per effetto di sentenza penale irrevocabile.`, `In caso di assenza prolungata dall'Italia per un periodo di almeno venti anni.`, `In caso di mancato esercizio del voto per un periodo non inferiore ai cinque anni.` ] , 
      "4939" : [ `Il diritto di voto:`, `Può essere limitato nei casi di indegnità morale indicati dalla legge.`, `Non può in alcun caso essere limitato.`, `Può essere limitato solo per incapacità civile.` ] , 
      "4940" : [ `Il diritto di voto - dispone la Costituzione - :`, `Può essere limitato per effetto di sentenza penale irrevocabile.`, `Non può in alcun caso essere limitato.`, `Può essere limitato solo per incapacità civile.` ] , 
      "4941" : [ `"Il diritto di voto spetta a tutti i cittadini, uomini e donne, che hanno raggiunto la maggiore età; esso è personale, eguale, libero, segreto e il suo esercizio è un dovere civico. Il carattere della personalità:"`, `Indica che il voto deve essere esercitato personalmente.`, `Significa che è vietato attribuire a ciascun voto un valore superiore all'unità.`, `Esclude la legittimità di qualunque coazione fisica o di condizionamento psichico nel suo esercizio.` ] , 
      "4942" : [ `"Il diritto di voto spetta a tutti i cittadini, uomini e donne, che hanno raggiunto la maggiore età; esso è personale, eguale, libero, segreto e il suo esercizio è un dovere civico. La libertà:"`, `Esclude la legittimità di qualunque coazione fisica o di condizionamento psichico nel suo esercizio.`, `Indica che il voto deve essere esercitato personalmente.`, `Significa che è vietato attribuire a ciascun voto un valore superiore all'unità.` ] , 
      "4943" : [ `"Il diritto di voto spetta a tutti i cittadini, uomini e donne, che hanno raggiunto la maggiore età; esso è personale, eguale, libero, segreto e il suo esercizio è un dovere civico. L'uguaglianza:"`, `Significa che è vietato attribuire a ciascun voto un valore superiore all'unità.`, `Indica che il voto deve essere esercitato personalmente.`, `Esclude la legittimità di qualunque coazione fisica o di condizionamento psichico nel suo esercizio.` ] , 
      "4944" : [ `"Il diritto di voto spetta a tutti i cittadini, uomini e donne, che hanno raggiunto la maggiore età; esso è personale, eguale, libero, segreto e il suo esercizio è un dovere civico. La segretezza:"`, `Viene assicurata con la predisposizione di strutture fisiche che rendono impossibile l'individuazione dell'autore del voto.`, `Indica che il voto deve essere esercitato personalmente.`, `Significa che è vietato attribuire a ciascun voto un valore superiore all'unità.` ] , 
      "4945" : [ `A norma della Costituzione, il diritto dei cittadini di associarsi in partiti politici è:`, `Libero.`, `Subordinato all'autorizzazione del Governo.`, `Libero, se lo statuto del partito preveda un ordinamento interno a base democratica.` ] , 
      "4946" : [ `A norma del dettato costituzionale, è corretto affermare che i cittadini possono rivolgere petizioni alle Camere solo per esporre comuni necessità?`, `No, la Costituzione dispone che tutti i cittadini possono rivolgere petizioni alle Camere per chiedere provvedimenti legislativi o esporre comuni necessità.`, `No, la Costituzione dispone che tutti i cittadini possono rivolgere petizioni alle Camere solo per chiedere provvedimenti legislativi.`, `Si, è l'unica ipotesi prevista dalla Costituzione.` ] , 
      "4947" : [ `A norma del dettato costituzionale, i cittadini possono rivolgere petizioni alle Camere per chiedere provvedimenti legislativi?`, `Si, lo dispone espressamente l'art. 50 della Costituzione.`, `No, possono rivolgere petizioni solo per chiedere l'amnistia.`, `No, possono rivolgere petizioni solo per chiedere l'abrogazione di una legge.` ] , 
      "4948" : [ `A norma del dettato costituzionale, i cittadini possono rivolgere petizioni alle Camere per:`, `Chiedere provvedimenti legislativi.`, `Richiedere referendum abrogativi.`, `Chiedere provvedimenti contingibili ed urgenti.` ] , 
      "4949" : [ `A norma del dettato costituzionale i cittadini rivolgono petizioni alle Camere per:`, `Esporre comuni necessità.`, `Rivendicare la tutela dei propri diritti.`, `Sollecitare l'approvazione di leggi in corso di discussione.` ] , 
      "4950" : [ `La Costituzione ammette la petizione al Governo?`, `No, la petizione è indirizzata al Parlamento.`, `Si.`, `No, la petizione è indirizzata al Presidente della Repubblica.` ] , 
      "4951" : [ `La petizione è indirizzata:`, `Al Parlamento.`, `Al Presidente della Repubblica.`, `Al Governo.` ] , 
      "4952" : [ `A norma della Costituzione, i cittadini possono accedere agli uffici pubblici:`, `In condizioni di eguaglianza, secondo i requisiti stabiliti dalla legge.`, `In condizioni di eguaglianza, salvo eccezioni previste dai regolamenti governativi.`, `In condizioni di eguaglianza, secondo i requisiti stabiliti con legge regionale.` ] , 
      "4953" : [ `Che diritto riconosce la Costituzione a chi è chiamato a funzioni pubbliche elettive?`, `Il diritto di disporre del tempo necessario al loro adempimento e di conservare il posto di lavoro.`, `Il diritto ad una retribuzione.`, `Il diritto alle prestazioni previdenziali.` ] , 
      "4954" : [ `A norma della Costituzione, chi è chiamato a funzioni pubbliche elettive:`, `Ha diritto di conservare il suo posto di lavoro.`, `Ha diritto alla conservazione del posto di lavoro, solo se il contratto è a tempo indeterminato.`, `Ha diritto alla conservazione del posto di lavoro, salvo i casi eccezionali previsti dallo statuto dei lavoratori.` ] , 
      "4955" : [ `"Quando la Costituzione afferma all'art. 52 che “è sacro dovere del cittadino”, a cosa si riferisce?"`, `Alla difesa della patria.`, `All'adempimento del servizio militare.`, `Al dovere di osservare le leggi.` ] , 
