      "4957" : [ `A norma della Costituzione, devono contribuire alle spese pubbliche:`, `Tutti i cittadini in ragione della loro capacità contributiva.`, `Tutti i cittadini nella stessa misura.`, `Solo i cittadini che utilizzano beni e servizi pubblici in ragione del loro utilizzo.` ] , 
      "4958" : [ `Il sistema tributario italiano - dispone l'art. 53 della Costituzione - è informato a criteri:`, `Di progressività.`, `Di proporzionalità.`, `Di pari opportunità.` ] , 
      "4959" : [ `Cosa dispone l'art. 53 della Costituzione in merito al sistema tributario italiano?`, `”"Il sistema tributario è informato a criteri di progressività”."`, `”"Il sistema tributario è informato a criteri di solidarietà”."`, `”"Il sistema tributario è informato a criteri di economicità”."` ] , 
      "4960" : [ `A quali criteri, a norma della Costituzione, è informato il sistema tributario italiano?`, `A criteri di progressività.`, `A criteri di uguaglianza.`, `A criteri di stabilità.` ] , 
