      "4963" : [ `Per quale delle seguenti categorie la Costituzione prevede che si possano stabilire limitazioni al diritto di iscriversi a partiti politici?`, `Per i magistrati.`, `Per i dirigenti delle amministrazioni locali.`, `Per gli avvocati.` ] , 
