﻿
Array.prototype["contains"] = function(this: Array<any>, match: string)
{
   return this.indexOf(match)>=0;
}

Array.prototype["removeAt"] = function(this: Array<any>, index: int)
{
   return this.splice(index, 1);
}

declare global 
{
   interface Array<T>
   {                                             
      contains(match: string): bool;
      removeAt(index: int): void;
   }
}

export {};



