// TODO eradicare
export function messagebox(msg)
{
   window.alert(msg);
}

export function repeat(s: string, n: int)
{
   if(n===0) return "";
   let a = new Array(n+1);
   return String(a.join(s));
}

export function rset(s: string|number, n: int, c: string)
{
   const p = repeat(c, n) + s;
   return right(p, n);
}

export function right(s: string, n: int)
{
   if(s.length < n) return s;      
   else return s.substr(s.length-n, n);
}

export function makeList(arr: string[])
{
   let result = "";

   arr.forEach((e,i)=>
   {
      result += arr;
           if(i<=arr.length-2) result += ", ";
      else if(i==arr.length-2) result += " e ";
   });
}

export function pluralize(n: int, singular: string, plural: string)
{
   if(n===1) return singular;
   else return plural;
}

export function uniqBy<T>(arr: T[], fieldName: string): T[]
{
   let mappa: {[key: string]: T} = {};
   arr.forEach(el => mappa[el[fieldName]]=el); 
   return Object.keys(mappa).map(key => mappa[key]);
}




