﻿import { hashHistory } from "react-router";

let go = (path: string|Object) => hashHistory.push(path);

class appRoutes
{
   comunica_malattie     () { go("/comunica_malattie");     }
   registro_ups          () { go("/registro_ups");          }
   dipendenti_elenco     () { go("/dipendenti_elenco");     }
   smaltimento_ferie     () { go("/smaltimento_ferie");     }
   calcola_uscita        () { go("/calcola_uscita");        }
   buoni_mensa           () { go("/buoni_mensa");           }      
   cud                   () { go("/cud");                   }   
   admin_user            () { go("/admin_user");            }
   pulsantiera_richieste () { go("/pulsantiera_richieste"); }
   richiesta_ferie       () { go("/richiesta_ferie");       }
   richiesta_recup       () { go("/richiesta_recup");       }
   richiesta_perm        () { go("/richiesta_perm");        }
   richiesta_l104        () { go("/richiesta_l104");        }
   richiesta_xtimbr      () { go("/richiesta_xtimbr");      }
   richiesta_timbr       () { go("/richiesta_timbr");       }
   login                 () { go("/login");                 }         
   forbidden             () { go("/forbidden");             }         
   processa_richieste    () { go("/processa_richieste");    }
   bancaore_solidale     () { go("/bancaore_solidale");     }
   resetpassword         () { go("/resetpassword");         }

   quizzone     () { go("/quizzone");     }

   password(linkHash?: string) 
   { 
      go(`/password/${linkHash||''}`);
   }

   prospetto_permessi(anno?: int) 
   { 
      go(`/prospetto_permessi/${anno||''}`);    
   }

   prospetto_ferie(anno?: int) 
   { 
      go(`/prospetto_ferie/${anno||''}`);    
   }

   richiesta_inviata(id: int)
   {
      go(`/richiesta_inviata/${id}`);
   }

   richieste(rich?: int)
   {
      go(`/richieste/${rich||''}`);
   }

   home() 
   {  
      go("/home");
   }

   smaltimento_bo(d?: Date)
   {
      if(d === undefined) go(`/smaltimento_bo`);
      else go(`/smaltimento_bo/${d.format("MM-yyyy")}`);      
   }

   timbrature_del_giorno(giorno: Date|undefined, matricola: int|string|undefined)
   {
      const day = giorno ? giorno : Date.today();
      const v = [ "timbrature_del_giorno", day.format("dd-MM-yyyy"), matricola ].map(el=>el?el:'');
      const s = `/${v.join("/")}`;

      go(s);
   }

   presenze_del_giorno(d?: Date)
   {
      if(d === undefined) go(`/presenze_del_giorno`);
      else go(`/presenze_del_giorno/${d.format("dd-MM-yyyy")}`);
   }   

   cedolino(d?: Date, matricola?: int) 
   {
      let path = [ "/cedolino" ];
      if(d !== undefined)  path.push(`${d.format("MM-yyyy")}`);
      if(matricola !== undefined) path.push(`${matricola}`);
      go(path.join("/"));
   }

   logout(expired?: boolean) 
   { 
      if(expired) go("/logout/expired");                 
      else        go("/logout");                 
   }

   error(exceptionMessage?: string) 
   { 
      go({pathname: "/error", state: {message: exceptionMessage}});                             
   }      
}

export const AppRoutes = new appRoutes();



