import "es6-shim";

// bootstrap 4 / reactstrap
// actually included as cdn link rel
// import 'bootstrap/dist/css/bootstrap.css';

// extension methods for Date, String
import "lib/date/extensionMethods";
import "lib/string";
import "lib/Array";

// publish components as global
import { DebugInfo } from "tags/_presentation/debug-info/debug-info"; window["DebugInfo"] = DebugInfo;
import { Icon }  from "tags/_presentation/icon/icon"; window["Icon"] = Icon;
import { Frame } from "tags/_presentation/Frame.html"; window["Frame"] = Frame;

// publish debug as global
function debug() { debugger; }
window["debug"] = debug;

// publish $h() React factory
import { createElement } from "react";

window["$h"] = createElement;

declare global 
{
   export var DebugInfo;
   export var Frame;
   export var Icon;
   export var debug;
   export var $h: typeof createElement;

   export var DEBUG: boolean;
   export var DEBUG_API: boolean; 
   export var DEBUG_STORE: boolean; 
   export var DEBUG_STORE_DIFF: boolean;
   export var DEBUG_CACHE: boolean;
   export var DEBUG_ROUTES: boolean;
   export var DEBUG_AUTOLOGIN: boolean;
}

window["DEBUG"]            = true;
window["DEBUG_API"]        = true;
window["DEBUG_STORE"]      = true;
window["DEBUG_STORE_DIFF"] = false;
window["DEBUG_CACHE"]      = true;
window["DEBUG_ROUTES"]     = true;
window["DEBUG_AUTOLOGIN"]  = true;   

// set error handler for all stores
import { handleFatalError } from "lib/api";
import { StoreTag } from "lib/StoreTag";
StoreTag.errorHandler = (store, err) => handleFatalError(err);   

if(DEBUG_STORE) 
{
   window["store"] = function(storename: string)
   {
      let state = window["$stores"][storename];
      if(state !== undefined) console.log(window["$stores"][storename]);
      else {
         if(storename !== undefined) console.log(`${storename} not found, these are the active stores:`);
         console.log(Object.keys(window["$stores"]));
         let ob = {};
         Object.keys(window["$stores"]).forEach(key => ob[key] = window["$stores"][key]);
         console.log(ob);
      }
   }
}

// start application
import { startApp } from "./main";
startApp();



