﻿import { Http } from "lib/http";
import { AppRoutes } from "tags/main-tag/appRoutes";
import { rset } from "lib/utils";

function debug(msg) 
{
   if(DEBUG_API) console.log(msg);
}

// se errore sul server durante la chiamata dell'API
// api.cshtml restituisce il seguente oggetto                                          

export interface ServerException 
{
   message: string;
   type: string;
   where?: string;
}

export class ServerError implements ServerException
{   
   constructor(public message: string, public type: string, public where?: string)
   {
   } 
}

export function isFatalError(err: ServerException) 
{
   return err.type !== "ClientException";
}

interface ApiResult 
{
   result?: any;
   error?:
   {
      message: string;
      type: string;
      where: string;
   }
}

export function handleFatalError(ex)
{
   if(ex instanceof ServerError && isFatalError(ex) && ex.type === 'NotLoggedException') 
   {
      AppRoutes.logout(true);
   }
   else 
   {
      AppRoutes.error(JSON.stringify(ex));
   }         
}

export class Api
{
   static lastMethod: string;

	static async call<T>(method, ...parameters)
	{
      Api.lastMethod = method;
            
      let jsonp = JSON.stringify(parameters);

      if(DEBUG_API)
      {
         let replacer = (s, n) => { let d = new Date(Number(n)); return `/Date(${n})/ ${rset(d.getDate(),2,"0")}/${rset(d.getMonth()+1,2,"0")}/${d.getFullYear()} ${rset(d.getHours(),2,"0")}:${rset(d.getMinutes(),2,"0")}:${rset(d.getSeconds(),2,"0")}`; };
         let decoded = jsonp.replace(/\/Date\(([0-9]*)\)\//, replacer);         
         debug(`Api '${method}' called with params='${decoded}'`);
      }
      
      try
      {
         // #newapi
         // const data = await Http.post("api.cshtml", { "method": method, "parameters": jsonp });
         const data = await Http.post("api.cshtml", { "method": method, "eparameters": encodeURIComponent(jsonp) });
		
         //if(DEBUG_API)
         //{
			//   debug(`Api '${method}' raw: '${JSON.stringify(data)}'`);
         //}

         let risp: ApiResult;

         try 
         {
			   risp = JSON.parseFull<ApiResult>(data); 
         }
         catch(err) 
         {
            if(DEBUG_API) debug(`Api '${method}' JSON parse error: ${err}, data: '${JSON.stringify(data)}'`);            
            throw new ServerError(err, 'JsonParseError');            
         }

			if(risp.result !== undefined)
			{
            //if(DEBUG_API) debug(`Api '${method}' returned: '${JSON.stringify(risp.result)}'`);          
            if(DEBUG_API) { debug(`Api '${method}' returned:`); debug(risp.result); }         
            return risp.result as T;            
			}
			else if(risp.error !== undefined)
			{				
            // if(DEBUG_API) debug(`Api '${method}' returned error: ${JSON.stringify(risp.error)}`);            
            if(DEBUG_API) { debug(`Api '${method}' returned error:`); debug(risp.error); }           
            throw new ServerError(risp.error.message, risp.error.type, risp.error.where);            
			}
         else 
         {            
				if(DEBUG_API) debug(`Api '${method}' returned unknown result: '${JSON.stringify(risp)}'`);
            throw new ServerError('unknown result', 'UnknownResult');
         }         
      }
      catch(err) 
      {
         if(err instanceof ServerError) 
         {
            if(isFatalError(err)) return Api.error(err);
            else throw err; // errore non fatale, handle separatamente           
         }                  
         else 
         {
            return Api.error(err);            
         }
      }                          
	}

   static error(ex: ServerException): never 
   {      
      console.error(`Api '${Api.lastMethod}' raised exception '${ex.type}' on server: ${ex.message}`);      
      
      // gli errori diversi da ClientException sono considerati gravi
      handleFatalError(ex);

      throw ex;
   }
}



