﻿import { hasToken, userManager, TokenPermessi } from "lib/User";
import { StoreTag } from "lib/StoreTag";
import { RouterNotifier } from "./RouterNotifier";

export class MainTag extends StoreTag
{
   render = require("./main-tag.html");

   constructor()
   {
      super();
      this.name = "main-tag";
   }

   token = (token?: TokenPermessi | undefined, guestAllowed?: boolean) =>
   {
      const onEnter = (nextState, replace) =>
      {
         if(!guestAllowed && !userManager.user.isLogged) {
            replace("/login");
            return;
         }

         if(token && !hasToken(token)) 
         {
            replace("/home");
         }                  
         
         RouterNotifier.changePath(nextState.location.pathname);

         if(DEBUG_ROUTES)
         {
            console.log(`route to: ${nextState.location.pathname}`);
         }
      }
      return onEnter;
   }   
}



