﻿// #if DEBUG_STORE
import { diff } from "deep-diff";
import { prettyDiff } from "lib/prettyDiff";
// #endif

declare global
{
   export var DEBUG_STORE: boolean;
}

export interface IUpdatable {
   update();
}

export class Store {
   private listeners: IUpdatable[] = [];
   private oldState: any;

   constructor(public name: string) {
   }

   update() {
      if(DEBUG_STORE_DIFF) this.showDiff();

      if(DEBUG_STORE) {
         if(window["$stores"] === undefined) window["$stores"] = [];
         window["$stores"][this.name] = (this as any).state;
      }

      this.listeners.forEach(listener => {
         // calls listener.update() with correct "this"
         listener.update.call(listener);
      });
   }

   subscribe(listener: IUpdatable) {
      if(typeof listener.update !== 'function') {
         throw `can't subscribe to store '${this.name}': missing 'update' function`;
      }

      if(this.listeners.filter(item => item===listener).length) {
         throw `already subscribed to store '${this.name}`;
      }

      this.listeners.push(listener);

      if(DEBUG_STORE) console.log(`store '${this.name}': subscribed`);
   }

   unsubscribe(listener) {
      this.listeners = this.listeners.filter(item => item!==listener);

      if(DEBUG_STORE) console.log(`store '${this.name}': unsubscribed`);
   }

   showDiff() {
      if(DEBUG_STORE)
      {
         const oldState = this.oldState;
         const newState = this["data"];

         const diffs = diff(oldState, newState);

         if(diffs)  {
            const header = `store '${this.name}': updated (${diffs.length} diffs)`;
            if(diffs.length>4) {
               console.groupCollapsed(header);
            } else {
               console.group(header);
            }
            diffs.forEach(d => console.log(prettyDiff(d)));
            console.groupEnd();
         } else {
            console.log(`store '${this.name}': updated (no diffs)`);
         }

         this.oldState = cloneDeep(newState);
      }
   }

   // @Store.action decorator
   static action(): any {
      if(DEBUG_STORE) {
         return (target: any, name: string, descriptor: TypedPropertyDescriptor<any>) => {
            const oldfun = target[name] as Function;

            descriptor.value = function(this: Store) {
               const _arguments = Array.prototype.slice.call(arguments);
               let args = JSON.stringify(_arguments);

               // remove trailing "[]"
               args = args.slice(1,args.length-1);

               console.log(`store '${this.name}': triggered action ${name}(${args})`);

               // hook before call
               this["oldState"] = cloneDeep((this as any).data);

               // execute action
               let ret;
               try
               {
                  ret = oldfun.apply(this, arguments);
               }
               catch(err)
               {
                  Store.error(this, err);
                  if(Store.errorHandler !== undefined) Store.errorHandler(this, err);
                  else throw err;
               }

               // check if async function
               if(ret instanceof Promise) {
                  return ret.catch((err)=> {
                     Store.error(this, err);
                     if(Store.errorHandler !== undefined) Store.errorHandler(this, err);
                     else throw err;
                  });
               }

               // hook after call
               return ret;
            };
         }
      }
   }

   static error(store: Store, exception) {
      if(DEBUG_STORE) {
         console.error(`store '${store.name}': exception raised while performing action`);
         console.error(exception);
      }
   }

   static errorHandler: (store: Store, err)=>void;
}

export function cloneDeep<T>(ob: T): T
{
   return JSON.parseFull<T>(JSON.stringify(ob));
}



