﻿/// <reference types="jasmine" />

import { cloneDeep } from "lib/store";
import { repeat, rset, right, uniqBy } from "lib/utils";
import { dateParse } from "lib/date/dateParse";
import { dateFormat } from "lib/date/dateFormat";
import { timeParse } from "lib/date/timeParse";
import { fromNow } from "lib/date/fromNow";
import { SQL } from "lib/DB";
import Rioct = require("rioct");

let RunSpecs: ()=>void;

export function runJasmineTests()
{
   Rioct.mount();
   RunSpecs();         
}

// simulates the old Jasmine 1.3 waitsFor()
function waitFor(F)
{
   beforeEach((done) => {
      setInterval(() => {
         if(F()) done();
      }, 250);
   });        
}

if(DEBUG) RunSpecs = function()
{   
   describe("String utils", () => {

      describe("repeat()", () => {
         it('repeat characters', () => {
            expect(repeat("0",3)).toEqual("000");         
            expect(repeat("ABC",3)).toEqual("ABCABCABC");         
            expect(repeat("ABC",0)).toEqual("");         
         });
      });

      describe("rset()", () => {
         it('rsets strings', () => {
            expect(rset("12",5,"0")).toEqual("00012");         
            expect(rset("124567",5,"0")).toEqual("24567");         
         });
      });

      describe("right()", () => {
         it('takes the rightmost part of a string', () => {
            expect(right("123",2)).toEqual("23");         
            expect(right("123",9)).toEqual("123");         
            expect(right("123",0)).toEqual("");         
         });
      });

   });

   describe("Date utils", () => {

      describe("Date.toString()", ()=>
      {
         it("formats date according to italian locale", ()=>
         {
            expect(new Date(73708225000).toString()).toEqual("03/05/1972 04:30:25.0");
         });
      });

      describe("Date.today()", ()=>
      {
         it("returns today's date at midnight", ()=>
         {
            var tod = Date.today();
            expect(tod.getDate()).toEqual(new Date().getDate());
            expect(tod.getMonth()).toEqual(new Date().getMonth());
            expect(tod.getFullYear()).toEqual(new Date().getFullYear());
            expect(tod.getHours()).toEqual(0);
            expect(tod.getMinutes()).toEqual(0);
            expect(tod.getSeconds()).toEqual(0);
            expect(tod.getMilliseconds()).toEqual(0);
         });
      });

      describe("Date.fromString()", ()=>
      {
         it("converts from d/M/yyyy h:mm:ss format", ()=>
         {  
            expect(Date.fromString("3/5/1972")).toEqual(new Date(1972,4,3));
            expect(Date.fromString("03/05/1972")).toEqual(new Date(1972,4,3));
            expect(Date.fromString("03/05/72")).toEqual(new Date(1972,4,3));
            expect(Date.fromString("03/05/17")).toEqual(new Date(2017,4,3));
            expect(Date.fromString("2:44")).toEqual(new Date(1970,0,1,2,44,0));
            expect(Date.fromString("12:44")).toEqual(new Date(1970,0,1,12,44,0));
            expect(Date.fromString("12:44:32")).toEqual(new Date(1970,0,1,12,44,32));
            expect(Date.fromString("03/05/1972 12:44:32")).toEqual(new Date(1972,4,3,12,44,32));
         });

         it("does not overflows when creating a date", ()=>
         {            
            expect(()=>Date.fromString("29/02/1973")).toThrow();
            expect(()=>Date.fromString("32/12/1950")).toThrow();
            expect(()=>Date.fromString("31/13/1950")).toThrow();
         });
      });

      describe("Date.fromDDMMAAAA()", ()=>
      {
         it("converts from DD/MM/AAAA format", ()=>
         {
            let a = Date.fromDDMMAAAA("03/05/1972");
            expect(a).toEqual(new Date(1972,4,3));
         });

         it("throws on invalid dates", ()=>
         {
            expect(()=>Date.fromDDMMAAAA("03/05/19/72")).toThrow();
         });

         it("does not overflows when creating a date", ()=>
         {            
            expect(()=>Date.fromDDMMAAAA("29/02/1973")).toThrow();
            expect(()=>Date.fromDDMMAAAA("32/12/1950")).toThrow();
            expect(()=>Date.fromDDMMAAAA("31/13/1950")).toThrow();
         });
      });

      describe("dateParse()", ()=> {

         let year = rset(Date.today().getFullYear(), 4, "0");
         let month = rset(Date.today().getMonth() + 1, 2, "0");

         it("converts a string into a full date", ()=> {            
            expect(dateParse("5")).toEqual(`05/${month}/${year}`);
            expect(dateParse("5/3")).toEqual(`05/03/${year}`);
            expect(dateParse(`05${month}`)).toEqual(`05/${month}/${year}`);
            expect(dateParse("31/12")).toEqual(`31/12/${year}`);
            expect(dateParse("5/3/16")).toEqual(`05/03/2016`);
            expect(dateParse("3/5/72")).toEqual(`03/05/1972`);            
            expect(dateParse("01/02/1980")).toEqual(`01/02/1980`);
            expect(dateParse("03-05-1972")).toEqual("03/05/1972");
            expect(dateParse("03.05.1972")).toEqual("03/05/1972");
            expect(dateParse("030572")).toEqual("03/05/1972");
            expect(dateParse("030516")).toEqual("03/05/2016");

            expect(dateParse("")).toBeUndefined();
            expect(dateParse("0")).toBeUndefined();
            expect(dateParse("34")).toBeUndefined();
            expect(dateParse("12/180")).toBeUndefined();
            expect(dateParse("29/02/1973")).toBeUndefined();            
            expect(dateParse("01/01/3180")).toBeUndefined();            
            expect(dateParse("01//1980")).toBeUndefined();
            expect(dateParse("/01/1980")).toBeUndefined();
            expect(dateParse("01/01/")).toBeUndefined();
            expect(dateParse("//")).toBeUndefined();
            expect(dateParse("01/01/1980/")).toBeUndefined();
         });
      });

      describe("timeParse()", ()=> {

         it("converts a string into a time", ()=> {
            expect(timeParse("0")).toEqual("0:00");
            expect(timeParse("5")).toEqual("5:00");
            expect(timeParse("5-3")).toEqual("5:03");
            expect(timeParse("730")).toEqual("7:30");
            expect(timeParse("23,59")).toEqual("23:59");
            expect(timeParse("23.59")).toEqual("23:59");

            expect(timeParse(":59")).toBeUndefined();
            expect(timeParse("12:")).toBeUndefined();
            expect(timeParse("")).toBeUndefined();
            expect(timeParse("34")).toBeUndefined();
            expect(timeParse("5:180")).toBeUndefined();
            expect(timeParse("64:12")).toBeUndefined();          
         });
      });

      describe("dateFormat()", ()=> {

         it("formats a JavaScript Date", ()=> {
            let d = new Date(2017, 3, 6, 7, 5, 2);
            expect(dateFormat(d, "d dd ddd Ddd dddd Dddd")).toEqual("6 06 gio Gio giovedì Giovedì");
            expect(dateFormat(d, "M MM MMM MMMM Mmm Mmmm")).toEqual("4 04 apr aprile Apr Aprile");            
            expect(dateFormat(d, "yy yyyy")).toEqual("17 2017");            
            expect(dateFormat(d, "h:mm:ss")).toEqual("7:05:02");            
            expect(dateFormat(d, "hh:mm:ss")).toEqual("07:05:02");            
         });
      });

      describe("fromNow()", ()=> {
         it("formats date in natural language", ()=> {
            let d = Date.fromString("27/04/2017 11:41:00"); 
            expect(fromNow(d, Date.fromString("27/04/2017 11:40:57"))).toBe("il 27 aprile alle 11:41");
            expect(fromNow(d, Date.fromString("27/04/2017 11:41:05"))).toBe("qualche secondo fa");
            expect(fromNow(d, Date.fromString("27/04/2017 11:42:00"))).toBe("un minuto fa");
            expect(fromNow(d, Date.fromString("27/04/2017 11:45:00"))).toBe("4 minuti fa");
            expect(fromNow(d, Date.fromString("27/04/2017 12:50:00"))).toBe("un'ora fa, alle 11:41");
            expect(fromNow(d, Date.fromString("27/04/2017 13:50:00"))).toBe("2 ore fa, alle 11:41");
            expect(fromNow(d, Date.fromString("27/04/2017 19:50:00"))).toBe("oggi alle 11:41");
            expect(fromNow(d, Date.fromString("28/04/2017 12:50:00"))).toBe("ieri alle 11:41");
            expect(fromNow(d, Date.fromString("29/04/2017 11:40:57"))).toBe("2 giorni fa, il 27 aprile alle 11:41");
            expect(fromNow(d, Date.fromString("03/05/2017 11:40:57"))).toBe("un mese fa, il 27 aprile alle 11:41");
            expect(fromNow(d, Date.fromString("27/05/2017 11:40:57"))).toBe("un mese fa, il 27 aprile alle 11:41");
            expect(fromNow(d, Date.fromString("27/06/2017 11:40:57"))).toBe("2 mesi fa, il 27 aprile alle 11:41");
            expect(fromNow(d, Date.fromString("27/04/2018 11:40:57"))).toBe("un anno fa, il 27 aprile alle 11:41");
            expect(fromNow(d, Date.fromString("27/04/2019 11:40:57"))).toBe("2 anni fa, il 27 aprile 2017 alle 11:41");
         });
      });
   });

   describe("SQL conversion utils", () => {
   
      describe("SQL.num()", ()=> {
         it("converts argument into a number for SQL command", ()=>{
            expect(SQL.num(null)).toBe("0");
            expect(SQL.num(undefined)).toBe("0");
            expect(SQL.num(42)).toBe("42");
            expect(SQL.num("42")).toBe("42");
            expect(SQL.num("pippo")).toBe("0");
         });
      });   

      describe("SQL.string()", ()=> {
         it("converts argument into a number for SQL command", ()=>{
            expect(SQL.string(null)).toBe("''");
            expect(SQL.string(undefined)).toBe("''");
            expect(SQL.string("")).toBe("''");
            expect(SQL.string("42")).toBe("'42'");
            expect(SQL.string("ARICO'")).toBe("'ARICO'''");
         });
      });   

      describe("SQL.date()", ()=> {
         it("converts argument into a number for SQL command", ()=>{
            const mindate = "CONVERT(DATETIME,'1899-12-30 00:00:00',102)";
            const d = new Date("1972-05-03T00:00:00.000");
            expect(SQL.date(null)).toBe(mindate);
            expect(SQL.date(undefined)).toBe(mindate);
            expect(SQL.date(d)).toBe("CONVERT(DATETIME,'1972-05-03 00:00:00',102)");
         });
      });   

      describe("SQL.datetime()", ()=> {
         it("converts argument into a number for SQL command", ()=>{
            const mindate = "CONVERT(DATETIME,'1899-12-30 00:00:00',102)";
            const d = new Date("1972-05-03T16:54:22.000");
            expect(SQL.datetime(null)).toBe(mindate);
            expect(SQL.datetime(undefined)).toBe(mindate);
            expect(SQL.datetime(d)).toBe("CONVERT(DATETIME,'1972-05-03 16:54:22',102)");
         });
      });   
   });

   describe("misc utils", () => {

      describe("Store.cloneDeep", () => {
         it("clones an object", ()=> {
            let source = { a: "some", b: "object", c: Date.fromString("03/05/1972") };
            let clone = cloneDeep(source);
            expect(clone).toEqual(source);
            expect(clone).not.toBe(source);
         });
      });

      describe("uniqBy", () => {
         it("groups rows in array of objects", ()=> {
            let source = [{ a: "4321", b: "Porcino"  },
                          { a: "4040", b: "Belfiore" },
                          { a: "4321", b: "Porcino"  }];
            let exp    = [{ a: "4321", b: "Porcino"  },
                          { a: "4040", b: "Belfiore" }];            
            let u = uniqBy(source, 'b');

            expect(u).toEqual(exp);
         });
      });

   });
}



