﻿import { StoreTag } from "lib/StoreTag";

import { getDomande } from "tags/quizzone/domande";

interface IState
{
   giuste: number;
   sbagliate: number;
   totali: number;
   idx: string;
   domanda: string;
   risposta: number;
   t1: string;
   t2: string;
   t3: string;
   scelta: number;
}

export class Quizzone extends StoreTag
{
   render = require("tags/quizzone/quizzone.html");

   state: IState = 
   {
      giuste: 0,
      sbagliate: 0,
      totali: 0,
      idx: "",
      domanda: "",
      risposta: 0,
      t1: "",
      t2: "",
      t3: "",
      scelta: -1
   }

   constructor(props)
   {
      super(props);
      this.name = "quizzone";
   }

   componentDidMount()
   {
      this.GET_DOMANDA();
   }

   handleRisposta(n: number)
   {
      this.SET_RISPOSTA(n);
   }

   handleNext()
   {
      this.NEXT_QUESTION();
   }

   @StoreTag.action()
   GET_DOMANDA()
   {
      let domande = getDomande();

      let n = Object.keys(domande).length;
      let idx = Math.floor(Math.random() * n);
      let key = Object.keys(domande)[idx];

      let domanda = domande[key][0];
      let risposta = domande[key][1];
      let errata1 = domande[key][2];
      let errata2 = domande[key][3];
      
      let arr  = [ risposta, errata1, errata2 ];
      let iarr = [ 1       , 0      , 0       ];

      function swap(a,i1,i2)
      {
         let tmp = a[i1];
         a[i1] = a[i2];
         a[i2] = tmp;
      }

      for(let t=0;t<3;t++)
      {
         let j = Math.floor(Math.random() * 3);
         swap(arr,t,j);
         swap(iarr,t,j);
      }      

      this.state.idx = key;
      this.state.domanda = domanda;
      this.state.risposta = iarr.indexOf(1);
      this.state.t1 = arr[0];
      this.state.t2 = arr[1];
      this.state.t3 = arr[2];
      this.state.scelta = -1;

      this.update();
   }

   @StoreTag.action()
   SET_RISPOSTA(n: number)
   {
      if(this.state.scelta === -1)
      {
         if(n === this.state.risposta) this.state.giuste++;
         else this.state.sbagliate++;
         this.state.totali++;
      }
      this.state.scelta = n;
      this.update();
   }

   @StoreTag.action()
   NEXT_QUESTION()
   {
      this.GET_DOMANDA();
   }
}


