﻿import { Observable } from "lib/observable";

class routeNotifier extends Observable
{
   currentPath: string;

   onchange(listener)
   {
      return this.on('change', listener);
   }

   off(id: number)
   {
      super.off(id);
   }   

   changePath(newPath: string)
   {
      if(DEBUG_ROUTES) console.log(`matched route ${newPath}`);
      this.currentPath = newPath;
      this.fire('change');
   }
}

export const RouterNotifier = new routeNotifier();




