﻿import { pluralize } from "lib/utils";

export function fromNow(d: Date, now?: Date): string
{
   now = now || (new Date());

   let oggi = now.datePart();
   let ieri = oggi.addDays(-1);

   let secs = (now.valueOf() - d.valueOf()) / 1000.0; 
   let secs_cut = (now.datePart().valueOf() - d.datePart().valueOf()) / 1000.0; 
   let mins = Math.floor(secs / 60);
   let ore = Math.floor(secs / (60*60));
   let gg = Math.ceil(secs_cut / (60*60*24));
   let months = (oggi.getFullYear()*12 + oggi.getMonth()) - (d.getFullYear()*12 + d.getMonth());
   let anni = oggi.getFullYear() - d.getFullYear();

   let full = d.format("il d MMMM alle h:mm");

   if(secs < 0) return full;
   if(secs < 60) return "qualche secondo fa";   
   if(mins < 60) return pluralize(mins, "un minuto", `${mins} minuti`) + " fa";
   
   if(ore < 4) return pluralize(ore, "un'ora", `${ore} ore`) + " fa, alle "+d.format("h:mm");
   
   if(d.datePart().equals(oggi)) return "oggi alle "+d.format("h:mm");
   if(d.datePart().equals(ieri)) return "ieri alle "+d.format("h:mm");

   if(months < 1) return `${gg} giorni fa, ` + full;
   
   if(months < 12) return pluralize(months, "un mese", `${months} mesi`)+" fa, " + full;
   
   if(anni == 1) return "un anno fa, " + full;

   return `${anni} anni fa, ` + d.format("il d MMMM yyyy alle h:mm");;
}




