﻿import React = require("react");

import { Button } from "reactstrap";

interface ButtonProps extends React.HTMLProps<HTMLButtonElement>
{
   to: ()=>void,
   disabled?: boolean,
   color?: string,
   size?: any
}

export function RouterButton({to, children, ...rest}: ButtonProps) 
{                  
   return <Button href="javascript: void(0)" onClick={(e)=>{e.stopPropagation(); to();}} {...rest}>{children}</Button>;
}




