﻿import React = require("react");

export const DebugInfo = (props:{data:any}) => 
{
   let expanded;
   
   try
   {
      expanded = JSON.stringify(props.data,null,2);   
   }
   catch(ex)
   {
      expanded = ex.message;
   } 
   return <pre>{expanded}</pre>; 
} 






